"use client";

import { useMemo, useCallback } from "react";
import {
  format,
  startOfMonth,
  endOfMonth,
  startOfWeek,
  endOfWeek,
  addDays,
  isSameMonth,
} from "date-fns";
import { Task } from "@/types/kanban";
import MonthGrid from "./MonthGrid";

interface MonthViewProps {
  tasks: Task[];
  currentDate: Date;
  projectStartedAt?: string;
  projectEndedAt?: string;
  onSelectSlot: (date: Date) => void;
  onSelectRange?: (startDate: Date, endDate: Date) => void;
  onSelectEvent: (task: Task) => void;
}

export default function MonthView({
  tasks,
  currentDate,
  projectStartedAt,
  projectEndedAt,
  onSelectSlot,
  onSelectRange,
  onSelectEvent,
}: MonthViewProps) {
  // ÏõîÏùò ÏãúÏûëÏùºÍ≥º ÎÅùÏùº
  const monthStart = useMemo(() => startOfMonth(currentDate), [currentDate]);
  const monthEnd = useMemo(() => endOfMonth(currentDate), [currentDate]);

  // Îã¨Î†•Ïóê ÌëúÏãúÌï† ÏãúÏûëÏùº (Ìï¥Îãπ Ïõî ÏãúÏûë Ï£ºÏùò ÏùºÏöîÏùº)
  const calendarStart = useMemo(
    () => startOfWeek(monthStart, { weekStartsOn: 0 }),
    [monthStart]
  );

  // Îã¨Î†•Ïóê ÌëúÏãúÌï† ÎÅùÏùº (Ìï¥Îãπ Ïõî ÎßàÏßÄÎßâ Ï£ºÏùò ÌÜ†ÏöîÏùº)
  const calendarEnd = useMemo(
    () => endOfWeek(monthEnd, { weekStartsOn: 0 }),
    [monthEnd]
  );

  // Îã¨Î†•Ïóê ÌëúÏãúÌï† Î™®Îì† ÎÇ†Ïßú (6Ï£º = 42Ïùº)
  const calendarDays = useMemo(() => {
    const days: Date[] = [];
    let day = calendarStart;
    while (day <= calendarEnd) {
      days.push(day);
      day = addDays(day, 1);
    }
    return days;
  }, [calendarStart, calendarEnd]);

  // Ï£º Îã®ÏúÑÎ°ú Í∑∏Î£πÌïë
  const weeks = useMemo(() => {
    const result: Date[][] = [];
    for (let i = 0; i < calendarDays.length; i += 7) {
      result.push(calendarDays.slice(i, i + 7));
    }
    return result;
  }, [calendarDays]);

  // ÎÇ†ÏßúÎ≥Ñ ÌÉúÏä§ÌÅ¨ Í∑∏Î£πÌïë
  const tasksByDate = useMemo(() => {
    const grouped: Record<string, Task[]> = {};

    tasks.forEach((task) => {
      if (!task.started_at && !task.ended_at) return;

      const startDate = task.started_at
        ? new Date(task.started_at)
        : new Date(task.ended_at!);
      const endDate = task.ended_at
        ? new Date(task.ended_at)
        : new Date(task.started_at!);

      // ÏãúÏûëÏùºÎ∂ÄÌÑ∞ Ï¢ÖÎ£åÏùºÍπåÏßÄ Î™®Îì† ÎÇ†ÏßúÏóê ÌÉúÏä§ÌÅ¨ Ï∂îÍ∞Ä
      let current = new Date(startDate);
      current.setHours(0, 0, 0, 0);
      const end = new Date(endDate);
      end.setHours(23, 59, 59, 999);

      while (current <= end) {
        const dateKey = format(current, "yyyy-MM-dd");
        if (!grouped[dateKey]) grouped[dateKey] = [];
        grouped[dateKey].push(task);
        current = addDays(current, 1);
      }
    });

    return grouped;
  }, [tasks]);

  // ÌîÑÎ°úÏ†ùÌä∏ Î≤îÏúÑ Ï≤¥ÌÅ¨
  const isOutsideProjectRange = useCallback(
    (date: Date) => {
      if (!projectStartedAt || !projectEndedAt) return false;
      const dateStr = format(date, "yyyy-MM-dd");
      return dateStr < projectStartedAt || dateStr > projectEndedAt;
    },
    [projectStartedAt, projectEndedAt]
  );

  // Ìï¥Îãπ ÏõîÏù∏ÏßÄ Ï≤¥ÌÅ¨
  const isCurrentMonth = useCallback(
    (date: Date) => isSameMonth(date, currentDate),
    [currentDate]
  );

  // ÏöîÏùº Ìó§Îçî
  const weekDayHeaders = ["Ïùº", "Ïõî", "Ìôî", "Ïàò", "Î™©", "Í∏à", "ÌÜ†"];

  return (
    <div className="flex flex-col h-full min-h-0 bg-white dark:bg-gray-900 overflow-hidden">
      {/* ÏöîÏùº Ìó§Îçî */}
      <div className="flex border-b border-gray-200 dark:border-gray-700 bg-gray-50 dark:bg-gray-800 shrink-0">
        {weekDayHeaders.map((day, index) => (
          <div
            key={day}
            className={`
              flex-1 p-1.5 sm:p-3 text-center text-xs sm:text-sm font-semibold border-r border-gray-200 dark:border-gray-700 last:border-r-0
              ${
                index === 0
                  ? "text-red-500 dark:text-red-400"
                  : index === 6
                  ? "text-blue-500 dark:text-blue-400"
                  : "text-gray-700 dark:text-gray-300"
              }
            `}
          >
            {day}
          </div>
        ))}
      </div>

      {/* ÎÇ†Ïßú Í∑∏Î¶¨Îìú */}
      <MonthGrid
        weeks={weeks}
        tasksByDate={tasksByDate}
        isOutsideProjectRange={isOutsideProjectRange}
        isCurrentMonth={isCurrentMonth}
        onSelectSlot={onSelectSlot}
        onSelectRange={onSelectRange}
        onSelectEvent={onSelectEvent}
      />
    </div>
  );
}
