"use client";

// React Î∞è ÎùºÏù¥Î∏åÎü¨Î¶¨
import { useCallback, useState, useMemo } from "react";
import { format } from "date-fns";

// ÌÉÄÏûÖ
import { Task, TaskPriority } from "@/types/kanban";

// Ïª¥Ìè¨ÎÑåÌä∏
import Modal from "@/components/ui/Modal";
import TaskAdd from "@/components/features/task/add/TaskAdd";
import TaskDetail from "@/components/features/task/detail/TaskDetail";
import CalendarHeader from "@/components/features/calendarView/components/CalendarHeader";
import CalendarHelp from "@/components/features/calendarView/components/CalendarHelp";
import CalendarStats from "@/components/features/calendarView/components/CalendarStats";
import CalendarFilter from "@/components/features/calendarView/components/CalendarFilter";
import CalendarNavigation from "@/components/features/calendarView/components/CalendarNavigation";
import WeekView from "@/components/features/calendarView/weekViews";
import MonthView from "@/components/features/calendarView/monthViews";
import DayView from "@/components/features/calendarView/dayViews";
import AgendaView from "@/components/features/calendarView/agendaViews";

// Hooks
import { useCalendarState } from "@/hooks/calendar/useCalendarState";
import { useCalendarKeyboard } from "@/hooks/calendar/useCalendarKeyboard";

// Ïú†Ìã∏
import { showToast } from "@/lib/utils/toast";

// ÌïÑÌÑ∞ Ïù∏ÌÑ∞ÌéòÏù¥Ïä§
interface CalendarFilter {
  priority: TaskPriority | "all";
  assignee: "all" | "assigned" | "unassigned" | "me";
  date: "all" | "today" | "thisWeek" | "overdue";
}

interface CalendarViewProps {
  tasks: Task[];
  boardId: string;
  project?: {
    project_id?: string;
    project_name: string;
    started_at?: string;
    ended_at?: string;
  } | null;
  onCreateTask?: (
    taskData: Omit<Task, "id" | "created_at" | "updated_at">
  ) => void;
  onUpdateTask?: (taskId: string, updates: Partial<Task>) => void;
  onDeleteTask?: (taskId: string) => void;
  onSelectTask?: (task: Task) => void;
  onTaskCreated?: () => void;
  onProjectInfoClick?: () => void;
}

export default function CalendarView({
  tasks = [],
  boardId,
  project,
  onCreateTask,
  onUpdateTask,
  onDeleteTask,
  onSelectTask,
  onTaskCreated,
  onProjectInfoClick,
}: CalendarViewProps) {
  // ÌîÑÎ°úÏ†ùÌä∏ Ï†ïÎ≥¥ Ï∂îÏ∂ú
  const projectId = project?.project_id || "";
  const projectName = project?.project_name || "Ïù¥Î¶Ñ ÏóÜÎäî ÌîÑÎ°úÏ†ùÌä∏";
  const projectStartedAt = project?.started_at;
  const projectEndedAt = project?.ended_at;

  console.log("CalendarView - Project Info:", {
    project,
    projectName,
    projectStartedAt,
    projectEndedAt,
    boardId,
    projectId,
  });

  // ÏÉÅÌÉú Í¥ÄÎ¶¨
  const {
    showTaskAddModal,
    setShowTaskAddModal,
    showTaskDetailModal,
    setShowTaskDetailModal,
    selectedTask,
    setSelectedTask,
    selectedDates,
    setSelectedDates,
    showHelp,
    setShowHelp,
    currentDate,
    setCurrentDate,
    currentView,
    setCurrentView,
  } = useCalendarState({
    projectStartedAt,
    projectEndedAt,
  });

  // ÌÇ§Î≥¥Îìú Îã®Ï∂ïÌÇ§
  useCalendarKeyboard({
    showTaskAddModal,
    showTaskDetailModal,
    currentDate,
    currentView,
    setShowTaskAddModal,
    setShowTaskDetailModal,
    setSelectedTask,
    setSelectedDates,
    setCurrentDate,
    setCurrentView,
  });

  // ÌïÑÌÑ∞ ÏÉÅÌÉú
  const [showFilter, setShowFilter] = useState(false);
  const [filter, setFilter] = useState<CalendarFilter>({
    priority: "all",
    assignee: "all",
    date: "all",
  });

  /**
   * ÌïÑÌÑ∞ÎßÅÎêú ÌÉúÏä§ÌÅ¨ Î™©Î°ù
   */
  const filteredTasks = useMemo(() => {
    return tasks.filter((task) => {
      // Ïö∞ÏÑ†ÏàúÏúÑ ÌïÑÌÑ∞
      if (filter.priority !== "all" && task.priority !== filter.priority) {
        return false;
      }

      // Îã¥ÎãπÏûê ÌïÑÌÑ∞ (me ÏòµÏÖòÏùÄ Î°úÏª¨ Î™®ÎìúÏóêÏÑú Ï†úÏô∏)
      if (filter.assignee !== "all") {
        switch (filter.assignee) {
          case "assigned":
            if (!task.assigned_user_id) return false;
            break;
          case "unassigned":
            if (task.assigned_user_id) return false;
            break;
        }
      }

      // ÎÇ†Ïßú ÌïÑÌÑ∞
      if (filter.date !== "all") {
        const today = new Date();
        const todayStr = format(today, "yyyy-MM-dd");

        switch (filter.date) {
          case "today":
            // Ïò§ÎäòÏù¥ ÏùºÏ†ï Î≤îÏúÑ(ÏãúÏûë~Ï¢ÖÎ£å) ÏïàÏóê Ìè¨Ìï®ÎêòÏñ¥Ïïº Ìï®
            if (task.started_at && task.ended_at) {
              const taskStartStr = task.started_at.split("T")[0];
              const taskEndStr = task.ended_at.split("T")[0];
              // Ïò§ÎäòÏù¥ ÏãúÏûëÏùº Ïù¥ÌõÑÏù¥Í≥†, Ï¢ÖÎ£åÏùº Ïù¥Ï†ÑÏù¥Ïñ¥Ïïº Ìï®
              if (todayStr < taskStartStr || todayStr > taskEndStr)
                return false;
            } else {
              // ÏãúÏûëÏùº/Ï¢ÖÎ£åÏùºÏù¥ ÏóÜÏúºÎ©¥ Ï†úÏô∏
              return false;
            }
            break;
          case "thisWeek":
            const weekStart = new Date(today);
            weekStart.setDate(today.getDate() - today.getDay());
            const weekEnd = new Date(weekStart);
            weekEnd.setDate(weekStart.getDate() + 6);
            const weekStartStr = format(weekStart, "yyyy-MM-dd");
            const weekEndStr = format(weekEnd, "yyyy-MM-dd");

            if (task.started_at && task.ended_at) {
              const taskStartStr = task.started_at.split("T")[0];
              const taskEndStr = task.ended_at.split("T")[0];
              // ÏùºÏ†ïÍ≥º Ïù¥Î≤à Ï£ºÍ∞Ä Í≤πÏπòÎäîÏßÄ ÌôïÏù∏
              if (taskEndStr < weekStartStr || taskStartStr > weekEndStr)
                return false;
            } else {
              return false;
            }
            break;
          case "overdue":
            if (task.status === "done") return false;
            if (!task.ended_at) return false;
            const taskEndStr2 = task.ended_at.split("T")[0];
            if (taskEndStr2 >= todayStr) return false;
            break;
        }
      }

      return true;
    });
  }, [tasks, filter, session]);

  /**
   * ÌïÑÌÑ∞ Î≥ÄÍ≤Ω Ìï∏Îì§Îü¨
   */
  const handleFilterChange = useCallback(
    (newFilter: Partial<CalendarFilter>) => {
      setFilter((prev) => ({ ...prev, ...newFilter }));
    },
    []
  );

  /**
   * ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Ìï∏Îì§Îü¨ (ÎÇ†Ïßú Ïù¥Îèô Ï†úÌïú Ìè¨Ìï®)
   */
  const handleNavigate = useCallback(
    (newDate: Date) => {
      console.log(
        "handleNavigate:",
        newDate,
        currentView,
        projectStartedAt,
        projectEndedAt
      );
      // ÌîÑÎ°úÏ†ùÌä∏ Í∏∞Í∞ÑÏù¥ ÏÑ§Ï†ïÎêòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞ Ï†úÌïú ÏóÜÏùå
      if (!projectStartedAt && !projectEndedAt) {
        console.log("ÌîÑÎ°úÏ†ùÌä∏ Í∏∞Í∞Ñ ÎØ∏ÏÑ§Ï†ï - ÎÇ†Ïßú Ïù¥Îèô ÌóàÏö©");
        setCurrentDate(newDate);
        return;
      }

      const minDate = new Date(projectStartedAt!);
      const maxDate = new Date(projectEndedAt!);

      // Î∑∞Ïóê Îî∞Îùº Îã§Î•¥Í≤å Ï≤¥ÌÅ¨
      if (currentView === "month") {
        // Ïõî Î∑∞: Ìï¥Îãπ ÏõîÏùò 1Ïùº Í∏∞Ï§Ä
        const checkDate = new Date(
          newDate.getFullYear(),
          newDate.getMonth(),
          1
        );
        const minMonth = new Date(minDate.getFullYear(), minDate.getMonth(), 1);
        const maxMonth = new Date(maxDate.getFullYear(), maxDate.getMonth(), 1);

        console.log("Ïõî Î∑∞ - ÎÇ†Ïßú Ïù¥Îèô Ï≤¥ÌÅ¨:", {
          minDate,
          maxDate,
          newDate,
        });

        if (checkDate < minMonth || checkDate > maxMonth) {
          showToast("ÌîÑÎ°úÏ†ùÌä∏ Í∏∞Í∞ÑÏùÑ ÌôïÏù∏ÌïòÏÑ∏Ïöî.", "warning");
          return;
        }
      } else {
        // Ï£º, Ïùº, ÏùºÏ†ï Î∑∞: Ìï¥Îãπ ÎÇ†Ïßú Í∏∞Ï§Ä
        if (newDate < minDate || newDate > maxDate) {
          showToast("ÌîÑÎ°úÏ†ùÌä∏ Í∏∞Í∞ÑÏùÑ ÌôïÏù∏ÌïòÏÑ∏Ïöî.", "warning");
          return;
        }
      }
      setCurrentDate(newDate);
    },
    [projectStartedAt, projectEndedAt, currentView, setCurrentDate]
  );

  /**
   * Task Ï∂îÍ∞Ä ÏÑ±Í≥µ Ìï∏Îì§Îü¨
   */
  const handleTaskAddSuccess = useCallback(
    async (taskData: Omit<Task, "id" | "created_at" | "updated_at">) => {
      await onCreateTask?.(taskData);
      setShowTaskAddModal(false);
      setSelectedDates(null);
      // onTaskCreated Ìò∏Ï∂ú Ï†úÍ±∞ - handleCreateTaskÏóêÏÑú Ïù¥ÎØ∏ Î°úÏª¨ ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏Ìï®
    },
    [onCreateTask, setShowTaskAddModal, setSelectedDates]
  );

  return (
    <>
      <div className="h-full bg-white dark:bg-gray-800 rounded-lg shadow-lg border border-gray-200 dark:border-gray-700 overflow-hidden flex flex-col">
        {/* Ï∫òÎ¶∞Îçî Ìó§Îçî */}
        <CalendarHeader
          projectName={projectName}
          projectStartedAt={projectStartedAt}
          projectEndedAt={projectEndedAt}
          currentView={currentView}
          currentDate={currentDate}
          eventsCount={filteredTasks.length}
          showHelp={showHelp}
          showFilter={showFilter}
          onToggleHelp={() => setShowHelp(!showHelp)}
          onToggleFilter={() => setShowFilter(!showFilter)}
          onProjectInfoClick={onProjectInfoClick}
          onAddTask={() => {
            const today = new Date();
            setSelectedDates({
              started_at: format(today, "yyyy-MM-dd"),
              ended_at: format(today, "yyyy-MM-dd"),
            });
            setShowTaskAddModal(true);
          }}
        />

        {/* ÎèÑÏõÄÎßê */}
        {showHelp && <CalendarHelp />}

        {/* ÌïÑÌÑ∞ */}
        {showFilter && (
          <div className="px-2 sm:px-4">
            <CalendarFilter
              filter={filter}
              onFilterChange={handleFilterChange}
              taskCount={filteredTasks.length}
              totalCount={tasks.length}
            />
          </div>
        )}

        {/* Ï∫òÎ¶∞Îçî Î≥∏Ï≤¥ */}
        <div className="flex-1 min-h-0 flex flex-col p-2 sm:p-3">
          {currentView === "week" ? (
            // Ïª§Ïä§ÌÖÄ Ï£ºÍ∞Ñ Î∑∞
            <div className="h-full flex flex-col rounded-lg border border-gray-200 dark:border-gray-600 overflow-hidden">
              {/* ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Ìà¥Î∞î */}
              <CalendarNavigation
                currentDate={currentDate}
                currentView={currentView}
                onNavigate={handleNavigate}
                onViewChange={setCurrentView}
              />
              <WeekView
                tasks={filteredTasks}
                currentDate={currentDate}
                projectStartedAt={projectStartedAt}
                projectEndedAt={projectEndedAt}
                onSelectSlot={(
                  startDate: Date,
                  endDate: Date,
                  startHour?: number,
                  endHour?: number
                ) => {
                  // ÌîÑÎ°úÏ†ùÌä∏ Ï¢ÖÎ£å Ï≤¥ÌÅ¨
                  if (projectEndedAt) {
                    const today = new Date().toISOString().split("T")[0];
                    if (today > projectEndedAt) {
                      showToast("Ï¢ÖÎ£åÎêú ÌîÑÎ°úÏ†ùÌä∏ÏûÖÎãàÎã§.", "warning");
                      return;
                    }
                  }

                  setSelectedDates({
                    started_at: format(startDate, "yyyy-MM-dd"),
                    ended_at: format(endDate, "yyyy-MM-dd"),
                    start_time:
                      startHour !== undefined
                        ? `${startHour.toString().padStart(2, "0")}:00`
                        : undefined,
                    end_time:
                      endHour !== undefined
                        ? `${endHour.toString().padStart(2, "0")}:00`
                        : undefined,
                  });
                  setShowTaskAddModal(true);
                }}
                onSelectEvent={(task) => {
                  setSelectedTask(task);
                  setShowTaskDetailModal(true);
                  onSelectTask?.(task);
                }}
              />
            </div>
          ) : currentView === "month" ? (
            // Ïª§Ïä§ÌÖÄ ÏõîÍ∞Ñ Î∑∞
            <div className="h-full flex flex-col rounded-lg border border-gray-200 dark:border-gray-600 overflow-hidden">
              {/* ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Ìà¥Î∞î */}
              <CalendarNavigation
                currentDate={currentDate}
                currentView={currentView}
                onNavigate={handleNavigate}
                onViewChange={setCurrentView}
              />
              <MonthView
                tasks={filteredTasks}
                currentDate={currentDate}
                projectStartedAt={projectStartedAt}
                projectEndedAt={projectEndedAt}
                onSelectSlot={(date: Date) => {
                  // ÌîÑÎ°úÏ†ùÌä∏ Ï¢ÖÎ£å Ï≤¥ÌÅ¨
                  if (projectEndedAt) {
                    const today = new Date().toISOString().split("T")[0];
                    if (today > projectEndedAt) {
                      showToast("Ï¢ÖÎ£åÎêú ÌîÑÎ°úÏ†ùÌä∏ÏûÖÎãàÎã§.", "warning");
                      return;
                    }
                  }

                  setSelectedDates({
                    started_at: format(date, "yyyy-MM-dd"),
                    ended_at: format(date, "yyyy-MM-dd"),
                  });
                  setShowTaskAddModal(true);
                }}
                onSelectRange={(startDate: Date, endDate: Date) => {
                  // ÌîÑÎ°úÏ†ùÌä∏ Ï¢ÖÎ£å Ï≤¥ÌÅ¨
                  if (projectEndedAt) {
                    const today = new Date().toISOString().split("T")[0];
                    if (today > projectEndedAt) {
                      showToast("Ï¢ÖÎ£åÎêú ÌîÑÎ°úÏ†ùÌä∏ÏûÖÎãàÎã§.", "warning");
                      return;
                    }
                  }

                  // ÎìúÎûòÍ∑∏Î°ú ÎÇ†Ïßú Î≤îÏúÑ ÏÑ†ÌÉù
                  setSelectedDates({
                    started_at: format(startDate, "yyyy-MM-dd"),
                    ended_at: format(endDate, "yyyy-MM-dd"),
                  });
                  setShowTaskAddModal(true);
                }}
                onSelectEvent={(task) => {
                  setSelectedTask(task);
                  setShowTaskDetailModal(true);
                  onSelectTask?.(task);
                }}
              />
            </div>
          ) : currentView === "day" ? (
            // Ïª§Ïä§ÌÖÄ ÏùºÍ∞Ñ Î∑∞
            <div className="h-full flex flex-col rounded-lg border border-gray-200 dark:border-gray-600 overflow-hidden">
              {/* ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Ìà¥Î∞î */}
              <CalendarNavigation
                currentDate={currentDate}
                currentView={currentView}
                onNavigate={handleNavigate}
                onViewChange={setCurrentView}
              />
              <DayView
                tasks={filteredTasks}
                currentDate={currentDate}
                projectStartedAt={projectStartedAt}
                projectEndedAt={projectEndedAt}
                onSelectSlot={(
                  date: Date,
                  startHour?: number,
                  endHour?: number
                ) => {
                  // ÌîÑÎ°úÏ†ùÌä∏ Ï¢ÖÎ£å Ï≤¥ÌÅ¨
                  if (projectEndedAt) {
                    const today = new Date().toISOString().split("T")[0];
                    if (today > projectEndedAt) {
                      showToast("Ï¢ÖÎ£åÎêú ÌîÑÎ°úÏ†ùÌä∏ÏûÖÎãàÎã§.", "warning");
                      return;
                    }
                  }

                  setSelectedDates({
                    started_at: format(date, "yyyy-MM-dd"),
                    ended_at: format(date, "yyyy-MM-dd"),
                    start_time:
                      startHour !== undefined
                        ? `${startHour.toString().padStart(2, "0")}:00`
                        : undefined,
                    end_time:
                      endHour !== undefined
                        ? `${endHour.toString().padStart(2, "0")}:00`
                        : undefined,
                  });
                  setShowTaskAddModal(true);
                }}
                onSelectEvent={(task) => {
                  setSelectedTask(task);
                  setShowTaskDetailModal(true);
                  onSelectTask?.(task);
                }}
              />
            </div>
          ) : (
            // Ïª§Ïä§ÌÖÄ ÏùºÏ†ï(Agenda) Î∑∞
            <div className="h-full flex flex-col rounded-lg border border-gray-200 dark:border-gray-600 overflow-hidden">
              {/* ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Ìà¥Î∞î */}
              <CalendarNavigation
                currentDate={currentDate}
                currentView={currentView}
                onNavigate={handleNavigate}
                onViewChange={setCurrentView}
              />
              <AgendaView
                tasks={filteredTasks}
                currentDate={currentDate}
                projectStartedAt={projectStartedAt}
                projectEndedAt={projectEndedAt}
                onSelectSlot={(date: Date) => {
                  // ÌîÑÎ°úÏ†ùÌä∏ Ï¢ÖÎ£å Ï≤¥ÌÅ¨
                  if (projectEndedAt) {
                    const today = new Date().toISOString().split("T")[0];
                    if (today > projectEndedAt) {
                      showToast("Ï¢ÖÎ£åÎêú ÌîÑÎ°úÏ†ùÌä∏ÏûÖÎãàÎã§.", "warning");
                      return;
                    }
                  }

                  setSelectedDates({
                    started_at: format(date, "yyyy-MM-dd"),
                    ended_at: format(date, "yyyy-MM-dd"),
                  });
                  setShowTaskAddModal(true);
                }}
                onSelectEvent={(task) => {
                  setSelectedTask(task);
                  setShowTaskDetailModal(true);
                  onSelectTask?.(task);
                }}
              />
            </div>
          )}
        </div>

        {/* ÌïòÎã®: ÌÜµÍ≥Ñ */}
        <div className="sm:hidden">
          <CalendarStats tasks={filteredTasks} compact />
        </div>
        <div className="hidden sm:block">
          <CalendarStats tasks={filteredTasks} />
        </div>
      </div>

      {/* TaskAdd Î™®Îã¨ */}
      {showTaskAddModal && selectedDates && (
        <Modal
          isOpen
          onClose={() => {
            setShowTaskAddModal(false);
            setSelectedDates(null);
          }}
        >
          <TaskAdd
            boardId={boardId}
            projectId={projectId}
            projectStartedAt={projectStartedAt}
            projectEndedAt={projectEndedAt}
            onSuccess={handleTaskAddSuccess}
            onCancel={() => {
              setShowTaskAddModal(false);
              setSelectedDates(null);
            }}
            initialStartDate={selectedDates.started_at}
            initialEndDate={selectedDates.ended_at}
            initialStartTime={selectedDates.start_time}
            initialEndTime={selectedDates.end_time}
            initialUseTime={
              !!(selectedDates.start_time || selectedDates.end_time)
            }
          />
        </Modal>
      )}

      {/* TaskDetail Î™®Îã¨ */}
      {showTaskDetailModal && selectedTask && (
        <Modal
          isOpen
          onClose={() => {
            setShowTaskDetailModal(false);
            setSelectedTask(null);
          }}
        >
          <TaskDetail
            task={{
              ...selectedTask,
              project_id: selectedTask.project_id || projectId,
            }}
            projectStartedAt={projectStartedAt}
            projectEndedAt={projectEndedAt}
            onUpdate={(taskId, updates) => {
              onUpdateTask?.(taskId, updates);
              onTaskCreated?.();
            }}
            onDelete={(taskId) => {
              onDeleteTask?.(taskId);
              setShowTaskDetailModal(false);
              setSelectedTask(null);
              onTaskCreated?.();
            }}
            onClose={() => {
              setShowTaskDetailModal(false);
              setSelectedTask(null);
            }}
          />
        </Modal>
      )}
    </>
  );
}
