"use client";

import { Task, TaskStatus } from "@/types/kanban";
import { format } from "date-fns";
import {
  getTaskStatusBarStyle,
  getTaskStatusDotColor,
  isTaskOverdue,
} from "@/lib/utils/taskUtils";

// Ïö∞ÏÑ†ÏàúÏúÑÎ≥Ñ ÏÇºÍ∞ÅÌòï ÏïÑÏù¥ÏΩò
const getPriorityIcon = (priority: string | undefined) => {
  switch (priority) {
    case "high":
      return <span className="text-red-500">‚ñ≤</span>;
    case "normal":
      return <span className="text-yellow-500">‚ñ≤</span>;
    case "low":
      return <span className="text-green-500">‚ñ≤</span>;
    default:
      return null;
  }
};

interface AgendaEventCardProps {
  tasks: Task[];
  onSelectEvent: (task: Task) => void;
}

// ÏãúÍ∞Ñ Ìè¨Îß∑ÌåÖ
const formatTime = (dateStr: string | null | undefined) => {
  if (!dateStr) return null;
  const date = new Date(dateStr);
  const hours = date.getHours();
  const minutes = date.getMinutes();
  if (hours === 0 && minutes === 0) return null; // Ï¢ÖÏùº ÏùºÏ†ï
  return format(date, "HH:mm");
};

// ÎÇ†Ïßú Î≤îÏúÑ Ìè¨Îß∑ÌåÖ
const formatDateRange = (
  startedAt: string | null | undefined,
  endedAt: string | null | undefined
) => {
  if (!startedAt && !endedAt) return "";

  const startStr = startedAt?.split("T")[0];
  const endStr = endedAt?.split("T")[0];

  if (startStr === endStr || !endStr) {
    return ""; // Í∞ôÏùÄ ÎÇ†Ïù¥Î©¥ Î≤îÏúÑ ÌëúÏãú ÏïàÌï®
  }

  const start = startedAt ? format(new Date(startedAt), "M/d") : "";
  const end = endedAt ? format(new Date(endedAt), "M/d") : "";

  return `${start} ~ ${end}`;
};

export default function AgendaEventCard({
  tasks,
  onSelectEvent,
}: AgendaEventCardProps) {
  if (tasks.length === 0) return null;

  return (
    <div className="space-y-2">
      {tasks.map((task) => {
        const startTime = formatTime(task.started_at);
        const endTime = formatTime(task.ended_at);
        const dateRange = formatDateRange(task.started_at, task.ended_at);
        const hasTime = startTime || endTime;
        const overdue = isTaskOverdue(task);

        return (
          <div
            key={task.id}
            className={`
              flex items-start gap-3 p-3 rounded-lg cursor-pointer
              hover:shadow-md transition-all duration-200
              ${getTaskStatusBarStyle(task.status as TaskStatus)}
            `}
            onClick={() => onSelectEvent(task)}
          >
            {/* ÏÉÅÌÉú ÎèÑÌä∏ + ÏßÄÏó∞ ÎèÑÌä∏ */}
            <div className="flex items-center gap-1 shrink-0 mt-0.5">
              <div
                className={`w-3 h-3 rounded-full ${getTaskStatusDotColor(
                  task.status as TaskStatus
                )}`}
              />
              {overdue && <div className="w-3 h-3 rounded-full bg-red-500" />}
            </div>

            {/* ÎÇ¥Ïö© */}
            <div className="flex-1 min-w-0">
              <div className="flex items-center gap-2">
                {/* Ïö∞ÏÑ†ÏàúÏúÑ ÏÇºÍ∞ÅÌòï */}
                {task.priority && (
                  <span className="text-xs">
                    {getPriorityIcon(task.priority)}
                  </span>
                )}
                {/* Ï†úÎ™© */}
                <span className="font-medium text-sm text-gray-900 dark:text-gray-100 truncate">
                  {task.title}
                </span>
              </div>

              {/* ÏãúÍ∞Ñ Î∞è Í∏∞Í∞Ñ Ï†ïÎ≥¥ */}
              <div className="flex items-center gap-2 mt-1 text-xs text-gray-500 dark:text-gray-400">
                {hasTime && (
                  <span className="flex items-center gap-1">
                    <svg
                      className="w-3 h-3"
                      fill="none"
                      stroke="currentColor"
                      viewBox="0 0 24 24"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z"
                      />
                    </svg>
                    {startTime}
                    {endTime && startTime !== endTime && ` ~ ${endTime}`}
                  </span>
                )}
                {dateRange && (
                  <span className="flex items-center gap-1">
                    <svg
                      className="w-3 h-3"
                      fill="none"
                      stroke="currentColor"
                      viewBox="0 0 24 24"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z"
                      />
                    </svg>
                    {dateRange}
                  </span>
                )}
                {!hasTime && !dateRange && (
                  <span className="text-gray-400 dark:text-gray-500">Ï¢ÖÏùº</span>
                )}
              </div>

              {/* ÏÑ§Î™Ö (ÏûàÏúºÎ©¥) */}
              {task.description && (
                <p className="mt-1.5 text-xs text-gray-600 dark:text-gray-400 line-clamp-2">
                  {task.description}
                </p>
              )}
            </div>

            {/* Ïò§Î•∏Ï™Ω ÌôîÏÇ¥Ìëú */}
            <svg
              className="w-4 h-4 text-gray-400 dark:text-gray-500 shrink-0"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M9 5l7 7-7 7"
              />
            </svg>
          </div>
        );
      })}
    </div>
  );
}
