"use client";

import { useEffect, useState } from "react";
import { UserAvatar } from "@/components/shared/UserAvatar";

/**
 * AssigneeInfo: ÌîÑÎ°úÏ†ùÌä∏ Îã¥ÎãπÏûê Ï†ïÎ≥¥Î•º ÌëúÏãúÌïòÎäî Ïª¥Ìè¨ÎÑåÌä∏
 * @param userId - Îã¥ÎãπÏûê user_id
 * @param projectId - ÌîÑÎ°úÏ†ùÌä∏ id
 */

interface AssigneeInfoProps {
  userId: string;
  projectId: string;
}

interface UserInfo {
  user_id: string;
  user_name: string;
  email: string;
  profile_image?: string;
  role: string;
}

const AssigneeInfo = ({ userId, projectId }: AssigneeInfoProps) => {
  const [userInfo, setUserInfo] = useState<UserInfo | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    /**
     * ÌîÑÎ°úÏ†ùÌä∏ Î©§Î≤Ñ Ï§ë userIdÏóê Ìï¥ÎãπÌïòÎäî ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ fetch
     */
    const fetchUserInfo = async () => {
      // projectId ÎòêÎäî userIdÍ∞Ä Ïú†Ìö®ÌïòÏßÄ ÏïäÏúºÎ©¥ API Ìò∏Ï∂ú Î∞©ÏßÄ
      if (
        !projectId ||
        projectId === "undefined" ||
        !userId ||
        userId === "undefined"
      ) {
        setUserInfo(null);
        setIsLoading(false);
        return;
      }

      try {
        const response = await fetch(
          `/api/projectMembers/forAssignment?projectId=${projectId}`
        );
        if (!response.ok) throw new Error("Failed to fetch user info");
        const result = await response.json();
        const member = result.data?.find((m: any) => m.user_id === userId);
        if (member) {
          setUserInfo({
            user_id: member.user_id,
            user_name: member.users.user_name,
            email: member.users.email,
            profile_image: member.users.profile_image,
            role: member.role,
          });
        }
      } catch {
        // ÎÑ§Ìä∏ÏõåÌÅ¨/ÌååÏã± ÏóêÎü¨ Ï≤òÎ¶¨
        setUserInfo(null);
      } finally {
        setIsLoading(false);
      }
    };
    fetchUserInfo();
  }, [userId, projectId]);

  // Î°úÎî© ÏÉÅÌÉú
  if (isLoading) {
    return (
      <div className="flex items-center gap-2">
        <UserAvatar
          userName="Î°úÎî©"
          profileImage={null}
          size={24}
          className="animate-pulse"
        />
        <span className="text-xs text-gray-400">Î∂àÎü¨Ïò§Îäî Ï§ë...</span>
      </div>
    );
  }

  // Îã¥ÎãπÏûê Ï†ïÎ≥¥ ÏóÜÏùå
  if (!userInfo) {
    return (
      <div className="flex items-center gap-2">
        <UserAvatar userName="?" profileImage={null} size={24} />
        <span className="text-xs text-gray-400 dark:text-gray-400">
          Îã¥ÎãπÏûê ÏóÜÏùå
        </span>
      </div>
    );
  }

  // Îã¥ÎãπÏûê Ï†ïÎ≥¥ ÌëúÏãú
  return (
    <div className="flex items-center gap-2">
      <UserAvatar
        userName={userInfo.user_name}
        profileImage={userInfo.profile_image}
        size={24}
      />
      <span className="text-xs text-gray-600 dark:text-gray-400">
        {userInfo.user_name}
      </span>
    </div>
  );
};

export default AssigneeInfo;
