"use client"; // Next.js ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ Ïª¥Ìè¨ÎÑåÌä∏ ÏßÄÏãúÏñ¥

// React Hooks - ÏÉÅÌÉú Í¥ÄÎ¶¨ÏôÄ ÏÉùÎ™ÖÏ£ºÍ∏∞
import { useState, useEffect } from "react";

// UI Ïª¥Ìè¨ÎÑåÌä∏Îì§
import Button from "@/components/ui/Button"; // Í≥µÌÜµ Î≤ÑÌäº Ïª¥Ìè¨ÎÑåÌä∏
import { Icon } from "@/components/shared/Icon"; // ÏïÑÏù¥ÏΩò Ïª¥Ìè¨ÎÑåÌä∏
import { showToast } from "@/lib/utils/toast"; // ÌÜ†Ïä§Ìä∏ ÏïåÎ¶º
import { TASK_MESSAGES } from "@/lib/constants/messages"; // Î©îÏãúÏßÄ ÏÉÅÏàò
import { useModal } from "@/hooks/useModal"; // Î™®Îã¨ ÏÉÅÌÉú Í¥ÄÎ¶¨ ÌõÖ
import Modal from "@/components/ui/Modal"; // Î™®Îã¨ Ïª¥Ìè¨ÎÑåÌä∏

// Task Í¥ÄÎ†® Í≥µÏö© Ïª¥Ìè¨ÎÑåÌä∏Îì§ - Ïû¨ÏÇ¨Ïö©ÏÑ±ÏùÑ ÏúÑÌï¥ Î∂ÑÎ¶¨
import { FormSection } from "@/components/features/task/shared/FormSection"; // Ìèº ÏÑπÏÖò ÎûòÌçº
import { StatusPrioritySection } from "@/components/features/task/shared/StatusPrioritySection"; // ÏÉÅÌÉú/Ïö∞ÏÑ†ÏàúÏúÑ
import { DateFields } from "@/components/features/task/shared/DateFields"; // ÎÇ†Ïßú ÏûÖÎ†• ÌïÑÎìúÎì§
import { SubtaskSection } from "@/components/features/task/shared/SubtaskSection"; // ÏÑúÎ∏åÌÉúÏä§ÌÅ¨ Í¥ÄÎ¶¨
import { AssigneeField } from "@/components/features/task/fields/AssigneeField"; // Îã¥ÎãπÏûê ÏÑ†ÌÉù

// ÌÉÄÏûÖ Ï†ïÏùò
import { Task } from "@/types/kanban";

// ============================================
// üõ†Ô∏è Ïú†Ìã∏Î¶¨Ìã∞ Ìï®ÏàòÎì§
// ============================================

/**
 * üìÖ ÎÇ†Ïßú/ÏãúÍ∞Ñ Î≥ÄÌôò Ïú†Ìã∏Î¶¨Ìã∞
 *
 * ISO Î¨∏ÏûêÏó¥Í≥º UI ÌëúÏãúÏö© ÎÇ†Ïßú/ÏãúÍ∞Ñ Í∞Ñ Î≥ÄÌôòÏùÑ Îã¥Îãπ
 * DB Ï†ÄÏû• ÌòïÏãùÍ≥º ÏÇ¨Ïö©Ïûê ÏπúÌôîÏ†Å ÌòïÏãù Í∞ÑÏùò Î∏åÎ¶øÏßÄ Ïó≠Ìï†
 */
const dateTimeUtils = {
  // üîÑ Ï†ÄÏû•Ïö©: ÎÇ†Ïßú+ÏãúÍ∞ÑÏùÑ ISO Î¨∏ÏûêÏó¥Î°ú Î≥ÄÌôò (DB Ï†ÄÏû•Ïö©)
  toISOString: (dateStr: string, timeStr?: string) => {
    const time = timeStr || "00:00"; // ÏãúÍ∞ÑÏù¥ ÏóÜÏúºÎ©¥ ÏûêÏ†ïÏúºÎ°ú
    return `${dateStr}T${time}:00.000Z`; // ISO 8601 ÌòïÏãù
  },

  // üé® ÌëúÏãúÏö©: ISO Î¨∏ÏûêÏó¥ÏùÑ ÎÇ†Ïßú/ÏãúÍ∞ÑÏúºÎ°ú Î∂ÑÎ¶¨ (UI ÌëúÏãúÏö©)
  parseDateTime: (isoString?: string | null) => {
    if (!isoString) return { date: "", time: "", hasTime: false };

    const [datePart, timePart] = isoString.split("T"); // ISO Î¨∏ÏûêÏó¥ ÌååÏã±
    const [hours, minutes] = timePart.split(":");

    return {
      date: datePart, // YYYY-MM-DD ÌòïÏãù
      time: `${hours}:${minutes}`, // HH:MM ÌòïÏãù
      hasTime: hours !== "00" || minutes !== "00", // Ïã§Ï†ú ÏãúÍ∞Ñ Ï†ïÎ≥¥ ÏûàÎäîÏßÄ ÌôïÏù∏
    };
  },
};

// ============================================
// üìã ÌÉÄÏûÖ Ï†ïÏùò
// ============================================

/**
 * TaskDetail Ïª¥Ìè¨ÎÑåÌä∏ Props Ïù∏ÌÑ∞ÌéòÏù¥Ïä§
 *
 * Î∂ÄÎ™® Ïª¥Ìè¨ÎÑåÌä∏(KanbanBoard)ÏóêÏÑú Ï†ÑÎã¨Î∞õÎäî Îç∞Ïù¥ÌÑ∞ÏôÄ ÏΩúÎ∞±Îì§
 */
interface TaskDetailProps {
  task: Task; // üéØ Ìé∏ÏßëÌï† Task Îç∞Ïù¥ÌÑ∞ (Î™®Îì† ÌïÑÎìú Ìè¨Ìï®)
  projectStartedAt?: string;
  projectEndedAt?: string;
  onUpdate?: (taskId: string, updates: Partial<Task>) => void; // üìù Task ÏóÖÎç∞Ïù¥Ìä∏ ÏΩúÎ∞±
  onDelete?: (taskId: string) => void; // üóëÔ∏è Task ÏÇ≠Ï†ú ÏΩúÎ∞±
  onClose?: () => void; // ‚ùå Î™®Îã¨ Îã´Í∏∞ ÏΩúÎ∞±
}

/**
 * ÌîÑÎ°úÏ†ùÌä∏ Î©§Î≤Ñ ÌÉÄÏûÖ
 *
 * Îã¥ÎãπÏûê ÏÑ†ÌÉù ÎìúÎ°≠Îã§Ïö¥ÏóêÏÑú ÏÇ¨Ïö©ÎêòÎäî Î©§Î≤Ñ Ï†ïÎ≥¥
 * Supabase JOIN ÏøºÎ¶¨ Í≤∞Í≥º Íµ¨Ï°∞
 */
type ProjectMember = {
  project_id: string; // üè∑Ô∏è ÌîÑÎ°úÏ†ùÌä∏ ID
  user_id: string; // üë§ ÏÇ¨Ïö©Ïûê ID
  role: string; // üé≠ Ïó≠Ìï† (leader/member)
  users: {
    // üë• ÏÇ¨Ïö©Ïûê ÏÉÅÏÑ∏ Ï†ïÎ≥¥ (JOIN)
    id: string; // ÏÇ¨Ïö©Ïûê Í≥†Ïú† ID
    name: string; // ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ
    email: string; // Ïù¥Î©îÏùº
    avatar_url: string; // ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄ URL
  };
};

// ============================================
// üéØ Î©îÏù∏ Ïª¥Ìè¨ÎÑåÌä∏
// ============================================

/**
 * TaskDetail Ïª¥Ìè¨ÎÑåÌä∏
 *
 * ÌïµÏã¨ Í∏∞Îä•:
 * - Task Î™®Îì† ÌïÑÎìú Ìé∏Ïßë (Ï†úÎ™©, ÏÑ§Î™Ö, ÏÉÅÌÉú, Ïö∞ÏÑ†ÏàúÏúÑ, ÎÇ†Ïßú, Îã¥ÎãπÏûê Îì±)
 * - Ïã§ÏãúÍ∞Ñ Î≥ÄÍ≤ΩÏÇ¨Ìï≠ Í∞êÏßÄ Î∞è Ï†ÄÏû• Î≤ÑÌäº ÌôúÏÑ±Ìôî
 * - ÎÇ†Ïßú/ÏãúÍ∞Ñ ÌòïÏãù Î≥ÄÌôò (ISO ‚Üî UI ÌëúÏãúÏö©)
 * - ÌîÑÎ°úÏ†ùÌä∏ Î©§Î≤Ñ Ï°∞Ìöå Î∞è Îã¥ÎãπÏûê Ìï†Îãπ
 * - ÏÑúÎ∏åÌÉúÏä§ÌÅ¨ Í¥ÄÎ¶¨
 * - ÏÇ≠Ï†ú ÌôïÏù∏ Î™®Îã¨
 */
export default function TaskDetail({
  task,
  projectStartedAt,
  projectEndedAt,
  onUpdate,
  onDelete,
  onClose,
}: TaskDetailProps) {
  // üîÑ ÏõêÎ≥∏ TaskÏùò ÎÇ†Ïßú Îç∞Ïù¥ÌÑ∞Î•º UI ÌòïÏãùÏúºÎ°ú ÌååÏã±
  const startDateTime = dateTimeUtils.parseDateTime(task.started_at);
  const endDateTime = dateTimeUtils.parseDateTime(task.ended_at);

  // üé® UIÏóêÏÑú Ìé∏ÏßëÌïòÍ∏∞ Ïâ¨Ïö¥ ÌòïÌÉúÎ°ú Task Îç∞Ïù¥ÌÑ∞ Î≥ÄÌôò
  const initialTask = {
    ...task, // ÏõêÎ≥∏ TaskÏùò Î™®Îì† ÌïÑÎìú Î≥µÏÇ¨
    started_at: startDateTime.date, // ISO ‚Üí YYYY-MM-DD
    ended_at: endDateTime.date,
    start_time: startDateTime.time, // ÏãúÍ∞Ñ Î∂ÄÎ∂Ñ Î∂ÑÎ¶¨
    end_time: endDateTime.time,
    use_time: startDateTime.hasTime || endDateTime.hasTime, // ÏãúÍ∞Ñ ÏÇ¨Ïö© Ïó¨Î∂Ä
  };

  // üìù ÏÉÅÌÉú Í¥ÄÎ¶¨
  const [editedTask, setEditedTask] = useState<Task>(initialTask); // Ìé∏Ïßë Ï§ëÏù∏ Task Îç∞Ïù¥ÌÑ∞
  const [editingField, setEditingField] = useState<string | null>(null); // ÌòÑÏû¨ Ìé∏Ïßë Ï§ëÏù∏ ÌïÑÎìú
  const [isLoadingMembers, setIsLoadingMembers] = useState(false); // Î©§Î≤Ñ Î°úÎî© ÏÉÅÌÉú
  const [isLoadingAssignee, setIsLoadingAssignee] = useState(false); // assignee Î≥¥Í∞ï Î°úÎî© ÏÉÅÌÉú
  const [members, setMembers] = useState<ProjectMember[] | null>(null); // ÌîÑÎ°úÏ†ùÌä∏ Î©§Î≤Ñ Î™©Î°ù
  const { openModal, modalProps } = useModal(); // ÏÇ≠Ï†ú ÌôïÏù∏ Î™®Îã¨ Í¥ÄÎ¶¨

  // ÌîÑÎ°úÏ†ùÌä∏ Ï¢ÖÎ£å ÏÉÅÌÉú Ï≤¥ÌÅ¨
  const isProjectEnded = (() => {
    if (!projectEndedAt) return false;
    const today = new Date().toISOString().split("T")[0];
    return today > projectEndedAt;
  })();

  // üöÄ Ïª¥Ìè¨ÎÑåÌä∏ ÎßàÏö¥Ìä∏ Ïãú ÌîÑÎ°úÏ†ùÌä∏ Î©§Î≤Ñ Îç∞Ïù¥ÌÑ∞ Ï°∞Ìöå
  useEffect(() => {
    /**
     * üë• ÌîÑÎ°úÏ†ùÌä∏ Î©§Î≤Ñ Ï°∞Ìöå ÎπÑÎèôÍ∏∞ Ìï®Ïàò
     *
     * Î™©Ï†Å: Îã¥ÎãπÏûê ÎìúÎ°≠Îã§Ïö¥Ïóê ÌëúÏãúÌï† Î©§Î≤Ñ Î™©Î°ù Ï°∞Ìöå
     * API ÏóîÎìúÌè¨Ïù∏Ìä∏: /api/projectMembers/forAssignment
     */
    const fetchMember = async () => {
      // üõ°Ô∏è Í∞ÄÎìú: ÌîÑÎ°úÏ†ùÌä∏ ID ÌïÑÏàò ÌôïÏù∏
      if (!task.project_id) {
        console.warn("ÌîÑÎ°úÏ†ùÌä∏ IDÍ∞Ä ÏóÜÏäµÎãàÎã§.");
        return;
      }

      setIsLoadingMembers(true); // Î°úÎî© ÏÉÅÌÉú ÏãúÏûë
      try {
        // üåê API Ìò∏Ï∂ú: ÌîÑÎ°úÏ†ùÌä∏ Î©§Î≤Ñ Î™©Î°ù ÏöîÏ≤≠
        const response = await fetch(
          `/api/projectMembers/forAssignment?projectId=${task.project_id}`
        );

        // üöë HTTP ÏóêÎü¨ Ï≤òÎ¶¨
        if (!response.ok) {
          const errorData = await response.json().catch(() => ({}));
          throw new Error(
            errorData.error ||
              `HTTP ${response.status}: ÌîÑÎ°úÏ†ùÌä∏ Î©§Î≤ÑÎ•º Î∂àÎü¨Ïò§Îäî Îç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§.`
          );
        }

        const result = await response.json();

        // üìà ÏùëÎãµ Îç∞Ïù¥ÌÑ∞ Ï≤òÎ¶¨
        if (result.data) {
          setMembers(result.data); // ÏÑ±Í≥µ: Î©§Î≤Ñ Î™©Î°ù ÏÑ§Ï†ï
        } else {
          console.warn("ÌîÑÎ°úÏ†ùÌä∏ Î©§Î≤Ñ Îç∞Ïù¥ÌÑ∞Í∞Ä ÏóÜÏäµÎãàÎã§:", result);
          setMembers([]); // Îç∞Ïù¥ÌÑ∞ ÏóÜÏùå: Îπà Î∞∞Ïó¥
        }
      } catch (error) {
        // üöë ÏòàÏô∏ Ï≤òÎ¶¨: ÎÑ§Ìä∏ÏõåÌÅ¨ Ïò§Î•ò, API ÏóêÎü¨ Îì±
        console.error("ÌîÑÎ°úÏ†ùÌä∏ Î©§Î≤Ñ Ï°∞Ìöå ÏóêÎü¨:", error);
        // ÏóêÎü¨Í∞Ä Î∞úÏÉùÌï¥ÎèÑ UIÍ∞Ä Íπ®ÏßÄÏßÄ ÏïäÎèÑÎ°ù Îπà Î∞∞Ïó¥Î°ú ÏÑ§Ï†ï
        setMembers([]);
      } finally {
        setIsLoadingMembers(false); // Î°úÎî© ÏÉÅÌÉú Ï¢ÖÎ£å
      }
    };

    fetchMember(); // ÎπÑÎèôÍ∏∞ Ìï®Ïàò Ïã§Ìñâ
  }, [
    task.project_id,
    task.id,
    task.assigned_user_id,
    task.title,
    task.kanban_board_id,
  ]); // ÏùòÏ°¥ÏÑ± Î∞∞Ïó¥: Ïù¥ Í∞íÎì§Ïù¥ Î≥ÄÍ≤ΩÎêòÎ©¥ Ïû¨Ïã§Ìñâ

  // 1Ïù∏ Í∞úÎ∞ú Î™®Îìú: assignee Ï†ïÎ≥¥ Î≥¥Í∞ïÏùÄ Î∂àÌïÑÏöî

  /**
   * üîç Î≥ÄÍ≤ΩÏÇ¨Ìï≠ Í∞êÏßÄ Ìï®Ïàò
   *
   * Î™©Ï†Å: ÏõêÎ≥∏ TaskÏôÄ Ìé∏ÏßëÎêú TaskÎ•º ÎπÑÍµêÌïòÏó¨ Î≥ÄÍ≤ΩÏÇ¨Ìï≠ Ï°¥Ïû¨ Ïó¨Î∂Ä ÌôïÏù∏
   * ÏÇ¨Ïö©: Ï†ÄÏû• Î≤ÑÌäº ÌôúÏÑ±Ìôî Ï°∞Í±¥, ÏàòÏ†ï ÏÇ¨Ìï≠ Í≤ΩÍ≥† Îì±
   *
   * ÎπÑÍµê ÎåÄÏÉÅ:
   * - Í∏∞Î≥∏ ÌïÑÎìú: title, description, status, priority, memo
   * - ÎÇ†Ïßú ÌïÑÎìú: started_at, ended_at, start_time, end_time, use_time
   * - Îã¥ÎãπÏûê: assigned_user_id
   * - ÏÑúÎ∏åÌÉúÏä§ÌÅ¨: subtasks Î∞∞Ïó¥ ÎπÑÍµê
   */
  const hasChanges = () => {
    // üîÑ ÏõêÎ≥∏ TaskÏùò ÎÇ†Ïßú Îç∞Ïù¥ÌÑ∞Î•º UI ÌòïÏãùÏúºÎ°ú ÌååÏã±
    const originalStart = dateTimeUtils.parseDateTime(task.started_at);
    const originalEnd = dateTimeUtils.parseDateTime(task.ended_at);

    return (
      editedTask.title !== task.title ||
      (editedTask.description || "") !== (task.description || "") ||
      editedTask.status !== task.status ||
      (editedTask.priority || "normal") !== (task.priority || "normal") ||
      (editedTask.assigned_user_id || null) !==
        (task.assigned_user_id || null) ||
      // ÎÇ†Ïßú ÎπÑÍµê
      editedTask.started_at !== originalStart.date ||
      editedTask.ended_at !== originalEnd.date ||
      (editedTask.use_time || false) !==
        (originalStart.hasTime || originalEnd.hasTime) ||
      (editedTask.use_time
        ? editedTask.start_time !== originalStart.time
        : false) ||
      (editedTask.use_time
        ? editedTask.end_time !== originalEnd.time
        : false) ||
      (editedTask.memo || "") !== (task.memo || "") ||
      JSON.stringify(editedTask.subtasks || []) !==
        JSON.stringify(task.subtasks || [])
    );
  };

  const handleChange = (field: keyof Task, value: any) => {
    setEditedTask((prev) => {
      const newData = { ...prev, [field]: value };

      // "ÏãúÍ∞Ñ ÏßÄÏ†ï" Ï≤¥ÌÅ¨ Ïãú Í∏∞Î≥∏ ÏãúÍ∞ÑÍ∞í ÏûêÎèô ÏÑ§Ï†ï
      if (field === "use_time" && value === true) {
        if (!newData.start_time) {
          newData.start_time = "09:00";
        }
        if (!newData.end_time) {
          newData.end_time = "18:00";
        }
      }

      // "ÏãúÍ∞Ñ ÏßÄÏ†ï" Ìï¥Ï†ú Ïãú ÏãúÍ∞ÑÍ∞í Ï¥àÍ∏∞Ìôî
      if (field === "use_time" && value === false) {
        newData.start_time = "";
        newData.end_time = "";
      }

      // ÏãúÍ∞ÑÏùÑ Î™®Îëê ÏßÄÏö∞Î©¥ use_timeÏùÑ falseÎ°ú ÏÑ§Ï†ï
      if (
        (field === "start_time" || field === "end_time") &&
        (!value || !value.trim())
      ) {
        const otherTimeField =
          field === "start_time" ? newData.end_time : newData.start_time;
        if (!otherTimeField || !otherTimeField.trim()) {
          console.log(
            `‚è∞ TaskDetail ÏãúÍ∞Ñ Î™®Îëê ÏÇ≠Ï†úÎê®, use_timeÏùÑ falseÎ°ú ÏÑ§Ï†ï`
          );
          newData.use_time = false;
        }
      }

      return newData;
    });
  };

  const handleSave = async () => {
    if (!hasChanges()) return;

    try {
      // ‚úÖ ÏàòÏ†ï: ÎÇ†ÏßúÎäî Ìï≠ÏÉÅ ÏûêÏ†ï, ÏãúÍ∞ÑÏùÄ Î≥ÑÎèÑ Ïª¨ÎüºÏóê Ï†ÄÏû•
      const startedAtISO = editedTask.started_at
        ? `${editedTask.started_at}T00:00:00`
        : null;

      const endedAtISO = editedTask.ended_at
        ? `${editedTask.ended_at}T00:00:00`
        : null;

      // Îç∞Ïù¥ÌÑ∞Î≤†Ïù¥Ïä§ ÏóÖÎç∞Ïù¥Ìä∏Ïö© - UI Ï†ÑÏö© ÌïÑÎìú Ï†úÏô∏
      const updates: Partial<Task> = {
        title: editedTask.title,
        description: editedTask.description,
        status: editedTask.status,
        priority: editedTask.priority,
        assigned_user_id: editedTask.assigned_user_id,
        started_at: startedAtISO,
        ended_at: endedAtISO,
        start_time: editedTask.use_time ? editedTask.start_time : null,
        end_time: editedTask.use_time ? editedTask.end_time : null,
        use_time: editedTask.use_time || false,
        memo: editedTask.memo,
        subtasks: editedTask.subtasks,
        updated_at: new Date().toISOString(),
      };

      // Î∂àÌïÑÏöîÌïú ÌïÑÎìú Ï†úÍ±∞ (DBÏóê ÏóÜÎäî Ïª¨ÎüºÎì§)
      const filteredUpdates = { ...updates };
      delete (filteredUpdates as any).id;
      delete (filteredUpdates as any).created_at;
      delete (filteredUpdates as any).kanban_boards;

      await onUpdate?.(task.id, filteredUpdates);
      showToast("ÏûëÏóÖÏù¥ Ï†ÄÏû•ÎêòÏóàÏäµÎãàÎã§.", "success");

      setTimeout(() => {
        onClose?.();
      }, 500);
    } catch (error) {
      console.error("ÏûëÏóÖ Ï†ÄÏû• Ï§ë Ïò§Î•ò:", error);
      showToast("ÏûëÏóÖ Ï†ÄÏû•Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.", "error");
    }
  };
  // ÏûëÏóÖ ÏÇ≠Ï†ú ÌôïÏù∏ Î™®Îã¨ Ïó¥Í∏∞
  const handleDelete = () => {
    openModal("delete", "ÏûëÏóÖ ÏÇ≠Ï†ú", TASK_MESSAGES.DELETE_CONFIRM);
  };

  // ÏûëÏóÖ ÏÇ≠Ï†ú Ïã§Ìñâ
  const confirmDelete = async () => {
    try {
      await onDelete?.(task.id);

      // ÏÇ≠Ï†ú ÏÑ±Í≥µ Î™®Îã¨ ÌëúÏãú
      openModal(
        "deleteSuccess",
        "ÏûëÏóÖ ÏÇ≠Ï†ú ÏôÑÎ£å",
        "ÏÑ†ÌÉùÌïú ÏûëÏóÖÏù¥ ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§."
      );

      // 5Ï¥à ÌõÑ ÏûêÎèôÏúºÎ°ú Î™®Îã¨ Îã´Í∏∞ (deleteSuccess Î™®Îã¨ÏùÄ ÏûêÎèô Îã´Ìûò)
      setTimeout(() => {
        onClose?.();
      }, 5000);
    } catch (error) {
      console.error("ÏûëÏóÖ ÏÇ≠Ï†ú Ï§ë Ïò§Î•ò:", error);
      openModal("error", "ÏÇ≠Ï†ú Ïã§Ìå®", "ÏûëÏóÖ ÏÇ≠Ï†ú Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
    }
  };

  return (
    <div className="task-form-layout">
      {/* Header - Í≥†Ï†ï */}
      <div className="shrink-0">
        <Header createdAt={task.created_at} updatedAt={task.updated_at} />
      </div>

      {/* Ïä§ÌÅ¨Î°§ Í∞ÄÎä•Ìïú Ïª®ÌÖêÏ∏† ÏòÅÏó≠ */}
      <div className="task-form-content space-y-5 mt-4">
        {/* Title */}
        <TitleField
          value={editedTask.title}
          isEditing={editingField === "title"}
          isProjectEnded={isProjectEnded}
          onEdit={() => !isProjectEnded && setEditingField("title")}
          onChange={(v: string) => handleChange("title", v)}
          onBlur={() => setEditingField(null)}
          onCancel={() => {
            setEditedTask(task);
            setEditingField(null);
          }}
        />

        {/* Status & Priority */}
        <StatusPrioritySection
          status={editedTask.status}
          priority={editedTask.priority || "normal"}
          disabled={isProjectEnded}
          onStatusChange={(v) => !isProjectEnded && handleChange("status", v)}
          onPriorityChange={(v) =>
            !isProjectEnded && handleChange("priority", v)
          }
        />

        {/* üìÑ ÏÑ§Î™Ö ÌïÑÎìú ÏÑπÏÖò - Ïù∏ÎùºÏù∏ Ìé∏Ïßë Í∞ÄÎä• */}
        <FormSection icon="description" title="ÏÑ§Î™Ö">
          <DescriptionField
            value={editedTask.description} // ÌòÑÏû¨ ÏÑ§Î™Ö ÎÇ¥Ïö©
            isEditing={editingField === "description"} // ÌòÑÏû¨ Ìé∏Ïßë Î™®Îìú Ïó¨Î∂Ä
            isProjectEnded={isProjectEnded}
            onEdit={() => !isProjectEnded && setEditingField("description")} // Ìé∏Ïßë Î™®Îìú ÏßÑÏûÖ
            onChange={(v: string) => handleChange("description", v)} // ÎÇ¥Ïö© Î≥ÄÍ≤Ω Ïãú ÏóÖÎç∞Ïù¥Ìä∏
            onBlur={() => setEditingField(null)} // Ìè¨Ïª§Ïä§ ÏûÉÏúºÎ©¥ Ìé∏Ïßë Î™®Îìú Ï¢ÖÎ£å
            onCancel={() => {
              // Ï∑®ÏÜå Ïãú ÏõêÎ≥∏ Îç∞Ïù¥ÌÑ∞Î°ú Î≥µÏõê
              setEditedTask(task);
              setEditingField(null);
            }}
          />
        </FormSection>

        {/* üë§ Îã¥ÎãπÏûê ÌïÑÎìú - ÌîÑÎ°úÏ†ùÌä∏ Î©§Î≤Ñ ÎìúÎ°≠Îã§Ïö¥ */}
        <AssigneeField
          value={editedTask.assigned_user_id} // ÌòÑÏû¨ Ìï†ÎãπÎêú ÏÇ¨Ïö©Ïûê ID
          isEditing={editingField === "assigned_user_id"} // Ìé∏Ïßë Î™®Îìú Ïó¨Î∂Ä
          isLoading={isLoadingMembers || isLoadingAssignee} // Î©§Î≤Ñ ÎòêÎäî assignee Î°úÎî© ÏÉÅÌÉú
          members={members} // ÌîÑÎ°úÏ†ùÌä∏ Î©§Î≤Ñ Î™©Î°ù (APIÏóêÏÑú Ï°∞Ìöå)
          disabled={isProjectEnded}
          onEdit={() => !isProjectEnded && setEditingField("assigned_user_id")} // Ìé∏Ïßë Î™®Îìú ÏßÑÏûÖ
          onChange={(v) => handleChange("assigned_user_id", v)} // Îã¥ÎãπÏûê Î≥ÄÍ≤Ω
          onBlur={() => setEditingField(null)} // Ìé∏Ïßë Î™®Îìú Ï¢ÖÎ£å
          onCancel={() => {
            // Ï∑®ÏÜå Ïãú ÏõêÎ≥∏ Îç∞Ïù¥ÌÑ∞Î°ú Î≥µÏõê
            setEditedTask(task);
            setEditingField(null);
          }}
        />

        {/* Dates ‚Üí AddÏôÄ ÎèôÏùºÌïú UI */}
        <FormSection icon="calendar" title="Í∏∞Í∞Ñ">
          <DateFields
            startDate={editedTask.started_at || ""}
            endDate={editedTask.ended_at || ""}
            startTime={editedTask.start_time || ""}
            endTime={editedTask.end_time || ""}
            useTime={editedTask.use_time || false}
            projectStartedAt={projectStartedAt}
            projectEndedAt={projectEndedAt}
            onStartDateChange={(v: string) => handleChange("started_at", v)}
            onEndDateChange={(v: string) => handleChange("ended_at", v)}
            onStartTimeChange={(v: string) => handleChange("start_time", v)}
            onEndTimeChange={(v: string) => handleChange("end_time", v)}
            onUseTimeChange={(v: boolean) => handleChange("use_time", v)}
          />
        </FormSection>

        {/* Subtasks */}
        <SubtaskSection
          subtasks={editedTask.subtasks || []}
          onUpdate={
            isProjectEnded
              ? undefined
              : (list) => handleChange("subtasks", list)
          }
          disabled={isProjectEnded}
        />

        {/* üìÉ Î©îÎ™® ÏÑπÏÖò - Ïù∏ÎùºÏù∏ Ìé∏Ïßë Í∞ÄÎä•Ìïú ÎÖ∏Ìä∏ ÌïÑÎìú */}
        <FormSection icon="notes" title="Î©îÎ™®">
          <MemoField
            value={editedTask.memo} // ÌòÑÏû¨ Î©îÎ™® ÎÇ¥Ïö©
            isEditing={editingField === "memo"} // ÌòÑÏû¨ Ìé∏Ïßë Î™®Îìú Ïó¨Î∂Ä
            isProjectEnded={isProjectEnded}
            onEdit={() => !isProjectEnded && setEditingField("memo")} // Ìé∏Ïßë Î™®Îìú ÏßÑÏûÖ
            onChange={(v: string) => handleChange("memo", v)} // Î©îÎ™® ÎÇ¥Ïö© Î≥ÄÍ≤Ω
            onBlur={() => setEditingField(null)} // Ìé∏Ïßë Î™®Îìú Ï¢ÖÎ£å
            onCancel={() => {
              // Ï∑®ÏÜå Ïãú ÏõêÎ≥∏ Îç∞Ïù¥ÌÑ∞Î°ú Î≥µÏõê
              setEditedTask(task);
              setEditingField(null);
            }}
          />
        </FormSection>
      </div>

      {/* Action Buttons - Sticky Footer */}
      <div className="task-form-footer bg-white dark:bg-gray-800">
        <ActionButtons
          hasChanges={hasChanges()}
          isProjectEnded={isProjectEnded}
          onCancel={() => setEditedTask(task)}
          onSave={handleSave}
          onDelete={handleDelete}
        />
      </div>

      {/* ÏÇ≠Ï†ú ÌôïÏù∏ Î™®Îã¨ */}
      <Modal {...modalProps} onConfirm={confirmDelete} />
    </div>
  );
}

