/**
 * üìù Header Ïª¥Ìè¨ÎÑåÌä∏ - Task Î©îÌÉÄ Ï†ïÎ≥¥ ÌëúÏãú
 *
 * Í∏∞Îä•:
 * - Task ÏÉùÏÑ±Ïùº Î∞è ÏàòÏ†ïÏùº ÌëúÏãú
 * - ÌïúÍµ≠Ïñ¥ ÎÇ†Ïßú Ìè¨Îß∑ ÏßÄÏõê
 * - ÏÉùÏÑ±/ÏàòÏ†ï Ïó¨Î∂Ä ÏûêÎèô Í∞êÏßÄ
 * - ÎØ∏Îûò ÌôïÏû•: ÏûëÏÑ±Ïûê Ï†ïÎ≥¥ Ï∂îÍ∞Ä ÏòàÏ†ï
 */
function Header({
  createdAt, // üìÖ Task ÏÉùÏÑ± ÏùºÏãú (ISO Î¨∏ÏûêÏó¥)
  updatedAt, // üîÑ Task ÏàòÏ†ï ÏùºÏãú (ISO Î¨∏ÏûêÏó¥)
}: {
  createdAt: string; // DBÏóêÏÑú Ïò§Îäî ÏÉùÏÑ± ÌÉÄÏûÑÏä§ÌÉ¨ÌîÑ
  updatedAt: string; // DBÏóêÏÑú Ïò§Îäî ÏàòÏ†ï ÌÉÄÏûÑÏä§ÌÉ¨ÌîÑ
}) {
  /**
   * üåè ÎÇ†Ïßú Ìè¨Îß§ÌåÖ Ïú†Ìã∏Î¶¨Ìã∞ Ìï®Ïàò
   *
   * ISO Î¨∏ÏûêÏó¥ÏùÑ ÌïúÍµ≠Ïñ¥ ÎÇ†Ïßú ÌòïÏãùÏúºÎ°ú Î≥ÄÌôò
   * Ïòà: "2024ÎÖÑ 11Ïõî 28Ïùº Ïò§ÌõÑ 2:30"
   */
  const formatDate = (dateString: string) => {
    const date = new Date(dateString); // ISO Î¨∏ÏûêÏó¥ÏùÑ Date Í∞ùÏ≤¥Î°ú Î≥ÄÌôò
    return date.toLocaleDateString("ko-KR", {
      year: "numeric", // 2024ÎÖÑ
      month: "long", // 11Ïõî
      day: "numeric", // 28Ïùº
      hour: "2-digit", // 14
      minute: "2-digit", // 30
    });
  };

  // üîç ÏÉùÏÑ± ÌõÑ ÏàòÏ†ï Ïó¨Î∂Ä ÌôïÏù∏ (ÏàòÏ†ï Î≤ÑÌäº ÌëúÏãúÏö©)
  const isUpdated = createdAt !== updatedAt;

  return (
    <div className="pb-4 border-b border-gray-200 dark:border-gray-700">
      <div className="flex flex-col gap-2 text-sm text-gray-500 dark:text-gray-400">
        {/* üìÖ Task ÏÉùÏÑ± Ï†ïÎ≥¥ ÌëúÏãú */}
        <div className="flex items-center gap-2">
          {/* ‚è∞ ÏãúÍ≥Ñ ÏïÑÏù¥ÏΩò */}
          <Icon
            type="clock"
            size={16}
            className="text-gray-400 dark:text-gray-500"
          />
          <span className="font-medium text-gray-600 dark:text-gray-400">
            ÏÉùÏÑ±:
          </span>
          <span>{formatDate(createdAt)}</span> {/* Ìè¨Îß§ÌåÖÎêú ÏÉùÏÑ±Ïùº */}
          {/* üï∞Ô∏è TODO: ÏûëÏÑ±Ïûê Ï†ïÎ≥¥ Ï∂îÍ∞Ä Ïãú ÏÇ¨Ïö© (Ïòà: "by ÍπÄÏ≤†Ïàò") */}
          {/* <span className="text-gray-400 dark:text-gray-500">by</span>
          <span className="font-medium text-gray-700 dark:text-gray-300">ÏûëÏÑ±ÏûêÎ™Ö</span> */}
        </div>

        {/* üîÑ Task ÏàòÏ†ï Ï†ïÎ≥¥ (Ï°∞Í±¥Î∂Ä Î†åÎçîÎßÅ: ÏàòÏ†ïÎêú Í≤ΩÏö∞Îßå ÌëúÏãú) */}
        {isUpdated && (
          <div className="flex items-center gap-2">
            {/* ‚úèÔ∏è Ìé∏Ïßë ÏïÑÏù¥ÏΩò */}
            <Icon
              type="edit"
              size={16}
              className="text-gray-400 dark:text-gray-500"
            />
            <span className="font-medium text-gray-600 dark:text-gray-400">
              ÏàòÏ†ï:
            </span>
            <span>{formatDate(updatedAt)}</span> {/* Ìè¨Îß§ÌåÖÎêú ÏàòÏ†ïÏùº */}
            {/* üï∞Ô∏è TODO: ÏàòÏ†ïÏûê Ï†ïÎ≥¥ Ï∂îÍ∞Ä Ïãú ÏÇ¨Ïö© (Ïòà: "by Ïù¥ÏòÅÌù¨") */}
            {/* <span className="text-gray-400 dark:text-gray-500">by</span>
            <span className="font-medium text-gray-700 dark:text-gray-300">ÏàòÏ†ïÏûêÎ™Ö</span> */}
          </div>
        )}
      </div>
    </div>
  );
}

/**
 * üìù TitleField Ïª¥Ìè¨ÎÑåÌä∏ - Ïù∏ÎùºÏù∏ Ìé∏Ïßë Í∞ÄÎä•Ìïú Ï†úÎ™© ÌïÑÎìú
 *
 * Í∏∞Îä•:
 * - ÏùΩÍ∏∞ Î™®Îìú: Ï†úÎ™© ÌëúÏãú + Ìé∏Ïßë Î≤ÑÌäº
 * - Ìé∏Ïßë Î™®Îìú: input ÌïÑÎìú + Ï†ÄÏû•/Ï∑®ÏÜå Î≤ÑÌäº
 * - ÏûêÎèô Ìè¨Ïª§Ïä§ Î∞è Ï†ÑÏ≤¥ ÏÑ†ÌÉù
 * - ESC ÌÇ§Î°ú Ï∑®ÏÜå Í∞ÄÎä•
 * - Îπà Í∞í Î∞©ÏßÄ Î∞è Ïú†Ìö®ÏÑ± Í≤ÄÏÇ¨
 */
function TitleField({
  value, // üè∑Ô∏è ÌòÑÏû¨ Ï†úÎ™© Í∞í
  isEditing, // ‚úèÔ∏è Ìé∏Ïßë Î™®Îìú Ïó¨Î∂Ä
  onEdit, // üëÜ Ìé∏Ïßë Î™®Îìú ÏßÑÏûÖ Ìï∏Îì§Îü¨
  onChange, // üîÑ Í∞í Î≥ÄÍ≤Ω Ìï∏Îì§Îü¨
  onBlur, // üëÅÔ∏è Ìè¨Ïª§Ïä§ Ïù¥ÌÉà Ìï∏Îì§Îü¨
  onCancel, // ‚ùå Ï∑®ÏÜå Ìï∏Îì§Îü¨
}: any) {
  if (isEditing) {
    return (
      <input
        type="text"
        value={value}
        onChange={(e) => onChange(e.target.value)}
        onBlur={onBlur}
        onKeyDown={(e) => {
          if (e.key === "Enter") onBlur();
          if (e.key === "Escape") onCancel();
        }}
        autoFocus
        className="text-2xl font-bold text-gray-800 dark:text-gray-200 w-full border-b-2 border-main-300 dark:border-main-600 focus:outline-none pb-2 bg-transparent"
      />
    );
  }

  return (
    <h2
      onClick={onEdit}
      className="text-2xl font-bold text-gray-800 dark:text-gray-200 cursor-pointer hover:text-main-500 dark:hover:text-main-400 transition-colors flex items-center gap-2"
    >
      <Icon
        type="edit"
        size={20}
        className="text-gray-600 dark:text-gray-400"
      />
      {value}
    </h2>
  );
}

function DescriptionField({
  value,
  isEditing,
  onEdit,
  onChange,
  onBlur,
  onCancel,
}: any) {
  return isEditing ? (
    <textarea
      value={value || ""}
      onChange={(e) => onChange(e.target.value)}
      onBlur={onBlur}
      onKeyDown={(e) => {
        if (e.key === "Escape") onCancel();
      }}
      autoFocus
      className="w-full px-3 py-2.5 border border-gray-300 dark:border-gray-600 rounded-lg input-focus-style min-h-[100px] bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 placeholder-gray-400 dark:placeholder-gray-500"
    />
  ) : (
    <p
      onClick={onEdit}
      className="text-gray-700 dark:text-gray-300 whitespace-pre-wrap cursor-pointer hover:bg-gray-50 dark:hover:bg-gray-700/50 p-3 rounded-lg min-h-[60px] transition-colors border border-transparent hover:border-gray-200 dark:hover:border-gray-600"
    >
      {value || (
        <span className="text-gray-400 dark:text-gray-500">
          ÌÅ¥Î¶≠ÌïòÏó¨ ÏÑ§Î™Ö Ï∂îÍ∞Ä
        </span>
      )}
    </p>
  );
}

function MemoField({
  value,
  isEditing,
  onEdit,
  onChange,
  onBlur,
  onCancel,
}: any) {
  if (isEditing) {
    return (
      <textarea
        value={value || ""}
        onChange={(e) => onChange(e.target.value)}
        onBlur={onBlur}
        onKeyDown={(e) => {
          if (e.key === "Escape") onCancel();
        }}
        autoFocus
        className="w-full px-3 py-2.5 border border-yellow-300 dark:border-yellow-700/50 bg-yellow-50 dark:bg-yellow-900/20 rounded-lg input-focus-style min-h-20 text-gray-900 dark:text-gray-100 placeholder-gray-400 dark:placeholder-gray-500"
      />
    );
  }

  return value ? (
    <div
      onClick={onEdit}
      className="bg-yellow-50 dark:bg-yellow-900/20 border border-yellow-200 dark:border-yellow-700/50 rounded-lg p-4 cursor-pointer hover:bg-yellow-100 dark:hover:bg-yellow-900/30 transition-colors"
    >
      <p className="text-gray-700 dark:text-gray-300 whitespace-pre-wrap">
        {value}
      </p>
    </div>
  ) : (
    <p
      onClick={onEdit}
      className="text-gray-400 dark:text-gray-500 cursor-pointer hover:bg-yellow-50 dark:hover:bg-yellow-900/20 p-4 border border-dashed border-yellow-300 dark:border-yellow-700/50 rounded-lg transition-colors"
    >
      ÌÅ¥Î¶≠ÌïòÏó¨ Î©îÎ™® Ï∂îÍ∞Ä
    </p>
  );
}

function ActionButtons({
  hasChanges,
  isProjectEnded,
  onCancel,
  onSave,
  onDelete,
}: any) {
  return (
    <div className="flex justify-between">
      {/* ÏÇ≠Ï†ú */}
      <Button
        btnType="form_s"
        variant="warning"
        onClick={onDelete}
        disabled={isProjectEnded}
      >
        {isProjectEnded ? "ÌîÑÎ°úÏ†ùÌä∏ Ï¢ÖÎ£åÎê®" : "ÏÇ≠Ï†ú"}
      </Button>

      {/* Ï∑®ÏÜå/Ï†ÄÏû• */}
      {hasChanges && (
        <div className="flex gap-3">
          <Button btnType="basic" variant="basic" onClick={onCancel}>
            Ï∑®ÏÜå
          </Button>
          <Button
            btnType="form"
            variant="primary"
            onClick={onSave}
            disabled={isProjectEnded}
          >
            {isProjectEnded ? "ÌîÑÎ°úÏ†ùÌä∏ Ï¢ÖÎ£åÎê®" : "Ï†ÄÏû•"}
          </Button>
        </div>
      )}
    </div>
  );
}
