"use client";

import { Icon } from "@/components/shared/Icon";
import { ReactNode } from "react";

interface EmptyStateProps {
  /** ÏïÑÏù¥ÏΩò ÌÉÄÏûÖ */
  icon?: string;
  /** ÏïÑÏù¥ÏΩò ÌÅ¨Í∏∞ */
  iconSize?: number;
  /** Î©îÏù∏ Ï†úÎ™© */
  title: string;
  /** Î∂ÄÏ†úÎ™©/ÏÑ§Î™Ö */
  description?: string;
  /** Ï∂îÍ∞Ä Ï†ïÎ≥¥ ÌÖçÏä§Ìä∏ */
  info?: string;
  /** Ïª§Ïä§ÌÖÄ Ïï°ÏÖò Î≤ÑÌäºÏù¥ÎÇò ÏöîÏÜå */
  action?: ReactNode;
  /** Î†àÏù¥ÏïÑÏõÉ Ïä§ÌÉÄÏùº */
  variant?: "default" | "dashed" | "minimal";
  /** Ï∂îÍ∞Ä CSS ÌÅ¥ÎûòÏä§ */
  className?: string;
}

/**
 * EmptyState - Îπà ÏÉÅÌÉúÎ•º ÌëúÏãúÌïòÎäî Í≥µÌÜµ Ïª¥Ìè¨ÎÑåÌä∏
 *
 * ÏÇ¨Ïö© ÏòàÏãú:
 * - ÌîÑÎ°úÏ†ùÌä∏ Î©§Î≤ÑÍ∞Ä ÏóÜÏùÑ Îïå
 * - Í≤ÄÏÉâ Í≤∞Í≥ºÍ∞Ä ÏóÜÏùÑ Îïå
 * - Îç∞Ïù¥ÌÑ∞Í∞Ä Î°úÎî©ÎêòÏßÄ ÏïäÏïòÏùÑ Îïå
 */
export function EmptyState({
  icon = "inbox",
  iconSize = 24,
  title,
  description,
  info,
  action,
  variant = "default",
  className = "",
}: EmptyStateProps) {
  const getVariantStyles = () => {
    switch (variant) {
      case "dashed":
        return "p-4 rounded-2xl border border-dashed border-gray-300 dark:border-gray-600 bg-gray-50 dark:bg-gray-800";
      case "minimal":
        return "p-2";
      default:
        return "p-4 rounded-lg border border-gray-200 dark:border-gray-600 bg-gray-50 dark:bg-gray-800";
    }
  };

  return (
    <div className={`text-center ${getVariantStyles()} ${className}`}>
      {/* ÏïÑÏù¥ÏΩò */}
      <Icon
        type={icon}
        size={iconSize}
        className="mx-auto mb-2 text-gray-400 dark:text-gray-500"
      />

      {/* Î©îÏù∏ Ï†úÎ™© */}
      <p className="text-sm text-gray-500 dark:text-gray-400 mb-1">{title}</p>

      {/* Î∂ÄÏ†úÎ™© */}
      {description && (
        <p className="text-sm text-gray-500 dark:text-gray-400 mb-2">
          {description}
        </p>
      )}

      {/* Ï∂îÍ∞Ä Ï†ïÎ≥¥ */}
      {info && (
        <p className="text-xs text-gray-400 dark:text-gray-500">{info}</p>
      )}

      {/* Ïï°ÏÖò */}
      {action && <div className="mt-3">{action}</div>}
    </div>
  );
}

// Ìé∏ÏùòÎ•º ÏúÑÌïú ÌîÑÎ¶¨ÏÖã Ïª¥Ìè¨ÎÑåÌä∏Îì§
