"use client";

import { useState } from "react";
import Image from "next/image";

interface UserAvatarProps {
  /** ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ */
  userName: string;
  /** ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄ URL */
  profileImage?: string | null;
  /** ÏïÑÎ∞îÌÉÄ ÌÅ¨Í∏∞ (ÌîΩÏÖÄ) */
  size?: number;
  /** Ï∂îÍ∞Ä CSS ÌÅ¥ÎûòÏä§ */
  className?: string;
  /** Ïù¥ÎØ∏ÏßÄ Î°úÎî© Ïã§Ìå® Ïãú ÏΩúÎ∞± */
  onImageError?: () => void;
}

/**
 * UserAvatar - ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄ ÎòêÎäî Ïù¥ÎãàÏÖúÏùÑ ÌëúÏãúÌïòÎäî Í≥µÌÜµ Ïª¥Ìè¨ÎÑåÌä∏
 *
 * Í∏∞Îä•:
 * - ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄÍ∞Ä ÏûàÏúºÎ©¥ Ïù¥ÎØ∏ÏßÄ ÌëúÏãú
 * - Ïù¥ÎØ∏ÏßÄÍ∞Ä ÏóÜÍ±∞ÎÇò Î°úÎî© Ïã§Ìå® Ïãú Ïù¥Î¶ÑÏùò Ï≤´ Í∏ÄÏûêÎ°ú ÎåÄÏ≤¥
 * - Îã§ÌÅ¨Î™®Îìú ÏßÄÏõê
 * - Ïª§Ïä§ÌÑ∞ÎßàÏù¥Ïßï Í∞ÄÎä•Ìïú ÌÅ¨Í∏∞
 */
export function UserAvatar({
  userName,
  profileImage,
  size = 32,
  className = "",
  onImageError,
}: UserAvatarProps) {
  const [imageError, setImageError] = useState(false);

  const isValidImageUrl = (url?: string | null) => {
    if (!url) return false;
    // default-user, placeholder Îì±Ïùò ÎçîÎØ∏ Ïù¥ÎØ∏ÏßÄ ÌïÑÌÑ∞ÎßÅ
    if (url.includes("default-user") || url.includes("placeholder"))
      return false;
    return true;
  };

  const handleImageError = () => {
    setImageError(true);
    onImageError?.();
  };

  const getInitial = () => {
    return userName?.charAt(0)?.toUpperCase() || "?";
  };

  // ÌÅ¨Í∏∞Ïóê Îî∞Î•∏ Ìè∞Ìä∏ ÌÅ¨Í∏∞ Í≥ÑÏÇ∞
  const getFontSize = () => {
    if (size <= 24) return "text-xs";
    if (size <= 32) return "text-sm";
    if (size <= 48) return "text-base";
    return "text-lg";
  };

  const avatarClasses = [
    `w-${Math.ceil(size / 4)} h-${Math.ceil(size / 4)}`, // Tailwind ÏÇ¨Ïù¥Ï¶à
    "rounded-full",
    "bg-main-200 dark:bg-main-700",
    "flex items-center justify-center",
    "overflow-hidden",
    className,
  ]
    .filter(Boolean)
    .join(" ");

  return (
    <div
      className={avatarClasses}
      style={{ width: size, height: size }} // Ï†ïÌôïÌïú ÌÅ¨Í∏∞Î•º ÏúÑÌïú Ïù∏ÎùºÏù∏ Ïä§ÌÉÄÏùº
    >
      {isValidImageUrl(profileImage) && !imageError ? (
        <Image
          src={profileImage!}
          alt={userName}
          width={size}
          height={size}
          className="w-full h-full object-cover"
          onError={handleImageError}
        />
      ) : (
        <span
          className={`font-medium text-main-600 dark:text-main-300 ${getFontSize()}`}
        >
          {getInitial()}
        </span>
      )}
    </div>
  );
}

// Ìé∏ÏùòÎ•º ÏúÑÌïú ÏÇ¨Ïù¥Ï¶àÎ≥Ñ ÌîÑÎ¶¨ÏÖã Ïª¥Ìè¨ÎÑåÌä∏Îì§
export function UserAvatarSmall(props: Omit<UserAvatarProps, "size">) {
  return <UserAvatar {...props} size={24} />;
}

export function UserAvatarMedium(props: Omit<UserAvatarProps, "size">) {
  return <UserAvatar {...props} size={32} />;
}

export function UserAvatarLarge(props: Omit<UserAvatarProps, "size">) {
  return <UserAvatar {...props} size={48} />;
}
