/**
 * Ï∫òÎ¶∞Îçî Ìó§Îçî Ïª¥Ìè¨ÎÑåÌä∏
 *
 * ÏÇ¨Ïö© ÏúÑÏπò: CalendarView ÏÉÅÎã®
 * ÌëúÏãú ÎÇ¥Ïö©: ÌîÑÎ°úÏ†ùÌä∏Î™Ö , Í∏∞Í∞Ñ, ÏßÑÌñâÎ•†, Î∑∞ Ï†ïÎ≥¥, ÎèÑÏõÄÎßê
 */

import { format } from "date-fns";
import { ko } from "date-fns/locale";
import { View } from "react-big-calendar";
import { VIEW_LABELS } from "../constants/calendarConfig";
import { showToast } from "@/lib/utils/toast";

interface CalendarHeaderProps {
  projectName: string;
  projectStartedAt?: string;
  projectEndedAt?: string;
  currentView: View;
  currentDate: Date;
  eventsCount: number;
  showHelp: boolean;
  showFilter?: boolean;
  onToggleHelp: () => void;
  onToggleFilter?: () => void;
  onAddTask?: () => void;
  onProjectInfoClick?: () => void;
}

export default function CalendarHeader({
  projectName,
  projectStartedAt,
  projectEndedAt,
  currentView,
  currentDate,
  eventsCount,
  showHelp,
  showFilter,
  onToggleHelp,
  onToggleFilter,
  onAddTask,
  onProjectInfoClick,
}: CalendarHeaderProps) {
  console.log(projectName);
  // Î∑∞Î≥Ñ ÎÇ†Ïßú Ìè¨Îß∑
  const getDateFormat = () => {
    switch (currentView) {
      case "month":
        return format(currentDate, "yyyyÎÖÑ MÏõî", { locale: ko });
      case "week":
        return format(currentDate, "MÏõî dÏùº Ï£º", { locale: ko });
      case "day":
        return format(currentDate, "MÏõî dÏùº (E)", { locale: ko });
      case "agenda":
        return "Ï†ÑÏ≤¥ ÏùºÏ†ï";
      default:
        return "";
    }
  };

  // Î∑∞ ÎùºÎ≤® Í∞ÄÏ†∏Ïò§Í∏∞ (ÌÉÄÏûÖ ÏïàÏ†ÑÌïòÍ≤å)
  const getViewLabel = () => {
    if (currentView in VIEW_LABELS) {
      return VIEW_LABELS[currentView as keyof typeof VIEW_LABELS];
    }
    return currentView;
  };

  /**
   * ÌîÑÎ°úÏ†ùÌä∏ Í∏∞Í∞Ñ Ï†ïÎ≥¥ Í≥ÑÏÇ∞ (KanbanHeaderÏôÄ ÎèôÏùºÌïú Î°úÏßÅ)
   */
  const getProjectPeriodInfo = () => {
    if (!projectStartedAt || !projectEndedAt) return null;

    const startDate = new Date(projectStartedAt);
    const endDate = new Date(projectEndedAt);
    const today = new Date();
    today.setHours(0, 0, 0, 0);

    const startStr = startDate.toLocaleDateString("ko-KR", {
      year: "numeric",
      month: "short",
      day: "numeric",
    });
    const endStr = endDate.toLocaleDateString("ko-KR", {
      year: "numeric",
      month: "short",
      day: "numeric",
    });

    // ÎÇ®ÏùÄ ÏùºÏàò Í≥ÑÏÇ∞
    const timeDiff = endDate.getTime() - today.getTime();
    const remainingDays = Math.ceil(timeDiff / (1000 * 3600 * 24));

    // ÏãúÏûëÍπåÏßÄ ÎÇ®ÏùÄ ÏùºÏàò
    const timeToStart = startDate.getTime() - today.getTime();
    const daysToStart = Math.ceil(timeToStart / (1000 * 3600 * 24));

    // ÌîÑÎ°úÏ†ùÌä∏ ÏÉÅÌÉú ÌåêÎã®
    let status: "before" | "active" | "warning" | "ended";
    let badgeText: string;
    let badgeColor: string;

    if (today < startDate) {
      // ÏãúÏûë Ï†Ñ
      status = "before";
      badgeText = `ÏãúÏûë D-${daysToStart}`;
      badgeColor = "bg-blue-500 text-white";
    } else if (today > endDate) {
      // Ï¢ÖÎ£å
      status = "ended";
      badgeText = "Ï¢ÖÎ£å";
      badgeColor = "bg-red-500 text-white";
    } else if (remainingDays <= 3) {
      // ÎßàÍ∞ê ÏûÑÎ∞ï (3Ïùº Ïù¥Ìïò)
      status = "warning";
      badgeText = `D-${remainingDays} ‚ö†Ô∏è`;
      badgeColor = "bg-orange-500 text-white";
    } else {
      // ÏßÑÌñâ Ï§ë
      status = "active";
      badgeText = `D-${remainingDays}`;
      badgeColor = "bg-white/20 text-white";
    }

    return {
      startStr,
      endStr,
      remainingDays,
      status,
      badgeText,
      badgeColor,
    };
  };

  const projectPeriod = getProjectPeriodInfo();

  // ÌîÑÎ°úÏ†ùÌä∏ Ï¢ÖÎ£å Ï≤¥ÌÅ¨ (KanbanHeaderÏôÄ ÎèôÏùºÌïú Î°úÏßÅ)
  const handleAddTaskClick = () => {
    if (projectStartedAt && projectEndedAt) {
      const today = new Date().toISOString().split("T")[0];

      if (today < projectStartedAt) {
        showToast("ÏïÑÏßÅ ÌîÑÎ°úÏ†ùÌä∏Í∞Ä ÏãúÏûëÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.", "warning");
        return;
      }

      if (today > projectEndedAt) {
        showToast("Ï¢ÖÎ£åÎêú ÌîÑÎ°úÏ†ùÌä∏ÏûÖÎãàÎã§.", "warning");
        return;
      }
    }
    onAddTask?.();
  };

  return (
    <div className="px-3 sm:px-6 py-3 sm:py-4 mb-2 sm:mb-4 border-b border-gray-200 dark:border-gray-500 bg-main-200 dark:bg-main-600 shadow-sm">
      <div className="flex items-center justify-between">
        {/* ÏôºÏ™Ω: Ï†úÎ™© + ÌîÑÎ°úÏ†ùÌä∏ Ï†ïÎ≥¥ Î≤ÑÌäº + Í∏∞Í∞Ñ */}
        <div className="flex flex-col gap-1">
          <div className="flex items-center gap-2">
            <h2 className="text-lg sm:text-xl font-bold text-white dark:text-gray-100">
              {projectName || "Ï∫òÎ¶∞Îçî"}
            </h2>
            {/* ÌîÑÎ°úÏ†ùÌä∏ Ï†ïÎ≥¥ Î≤ÑÌäº */}
            {onProjectInfoClick && (
              <button
                onClick={onProjectInfoClick}
                className="p-1 hover:bg-white/20 rounded-lg transition-colors"
                title="ÌîÑÎ°úÏ†ùÌä∏ Ï†ïÎ≥¥"
              >
                <svg
                  className="w-5 h-5 text-white/80 hover:text-white"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
                  />
                </svg>
              </button>
            )}
          </div>
          {/* ÌîÑÎ°úÏ†ùÌä∏ Í∏∞Í∞Ñ & ÏÉÅÌÉú Î±ÉÏßÄ (KanbanHeaderÏôÄ ÎèôÏùº) */}
          {projectPeriod && (
            <div className="flex items-center gap-2 text-xs text-white/80 dark:text-gray-200">
              <span>|</span>
              <span>
                {projectPeriod.startStr} ~ {projectPeriod.endStr}
              </span>
              <span
                className={`px-2 py-0.5 rounded-full font-medium ${projectPeriod.badgeColor}`}
              >
                {projectPeriod.badgeText}
              </span>
            </div>
          )}
          {/* Ï¢ÖÎ£åÎêú ÌîÑÎ°úÏ†ùÌä∏ ÏïàÎÇ¥ Î¨∏Íµ¨ */}
          {projectPeriod?.status === "ended" && (
            <div className="flex items-center gap-1.5 text-xs bg-sky-100 dark:bg-sky-900/50 text-sky-700 dark:text-sky-200 px-2.5 py-1 rounded-md border border-sky-300 dark:border-sky-700">
              <svg
                className="w-4 h-4 shrink-0"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
                />
              </svg>
              <span className="font-medium">
                Ïù¥ ÌîÑÎ°úÏ†ùÌä∏Îäî Ï¢ÖÎ£åÎêòÏóàÏäµÎãàÎã§. ÏùºÏ†ï Ï∂îÍ∞Ä/ÏàòÏ†ïÏù¥ Ï†úÌïúÎê©ÎãàÎã§.
              </span>
            </div>
          )}
        </div>

        {/* Ïò§Î•∏Ï™Ω: Î∑∞/ÏùºÏ†ï/ÎèÑÏõÄÎßê */}
        <div className="flex items-center gap-2 sm:gap-3">
          {/* ÌòÑÏû¨ Î∑∞ ÌëúÏãú */}
          <div className="hidden sm:flex items-center gap-2">
            <div className="px-2 py-1 bg-white/20 rounded-full text-xs text-white font-medium">
              {getViewLabel()}
            </div>
          </div>

          {/* ÏùºÏ†ï Í∞úÏàò Î∞è ÎÇ†Ïßú */}
          <div className="text-sm font-medium text-white/90 dark:text-gray-200 text-right">
            <div>{eventsCount}Í∞ú ÏùºÏ†ï</div>
            <div className="text-xs text-white/70 dark:text-gray-300">
              {getDateFormat()}
            </div>
          </div>

          {/* ÏÉà ÏûëÏóÖ Î≤ÑÌäº (KanbanHeaderÏôÄ ÎèôÏùº) */}
          {onAddTask && (
            <button
              onClick={handleAddTaskClick}
              className="px-3 sm:px-4 py-2 bg-main-400 dark:bg-main-500 text-white rounded-lg hover:bg-main-500 dark:hover:bg-main-400 active:bg-main-600 dark:active:bg-main-600 transition-all text-xs sm:text-sm font-medium shadow-sm"
            >
              <span className="hidden sm:inline">+ ÏÉà ÏûëÏóÖ</span>
              <span className="sm:hidden">+</span>
            </button>
          )}

          {/* ÌïÑÌÑ∞ Î≤ÑÌäº */}
          {onToggleFilter && (
            <button
              onClick={onToggleFilter}
              className={`p-1.5 sm:p-2 hover:bg-white/20 rounded-lg transition-colors ${
                showFilter ? "bg-white/20" : ""
              }`}
              title="ÌïÑÌÑ∞"
            >
              <svg
                className="w-4 h-4 sm:w-5 sm:h-5 text-white"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M3 4a1 1 0 011-1h16a1 1 0 011 1v2.586a1 1 0 01-.293.707l-6.414 6.414a1 1 0 00-.293.707V17l-4 4v-6.586a1 1 0 00-.293-.707L3.293 7.414A1 1 0 013 6.707V4z"
                />
              </svg>
            </button>
          )}

          {/* ÎèÑÏõÄÎßê Î≤ÑÌäº */}
          <button
            onClick={onToggleHelp}
            className="p-1.5 hover:bg-white/20 rounded-lg transition-colors"
            title={showHelp ? "ÎèÑÏõÄÎßê Îã´Í∏∞" : "ÎèÑÏõÄÎßê Ïó¥Í∏∞"}
          >
            <svg
              className={`w-4 h-4 text-white transition-transform duration-300 ${
                showHelp ? "scale-110" : ""
              }`}
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M8.228 9c.549-1.165 2.03-2 3.772-2 2.21 0 4 1.343 4 3 0 1.4-1.278 2.575-3.006 2.907-.542.104-.994.54-.994 1.093m0 3h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
              />
            </svg>
          </button>
        </div>
      </div>
    </div>
  );
}
