/**
 * Ïπ∏Î∞òÎ≥¥Îìú ÌÜµÍ≥Ñ + Î≤îÎ°Ä Ïª¥Ìè¨ÎÑåÌä∏
 *
 * ÌëúÏãú ÎÇ¥Ïö©:
 * - ÌÜµÍ≥Ñ: Ìï†Ïùº/ÏßÑÌñâÏ§ë/ÏôÑÎ£å/ÏßÄÏó∞/Í∏¥Í∏â(Ïò§Îäò ÎßàÍ∞ê, D-3 Ïù¥ÎÇ¥)
 * - Î≤îÎ°Ä: ÏßÄÏó∞ ÌëúÏãú, ÏôÑÎ£å ÌëúÏãú, Ïö∞ÏÑ†ÏàúÏúÑ ÏïÑÏù¥ÏΩò
 * - ÏßÑÌñâÎ•† ÌëúÏãú
 */

import { useMemo } from "react";
import { Task } from "@/types/kanban";
import { format, addDays } from "date-fns";
import { AlertTriangle, Clock } from "lucide-react";

interface KanbanLegendProps {
  tasks?: Task[];
  compact?: boolean;
}

export default function KanbanLegend({
  tasks = [],
  compact = false,
}: KanbanLegendProps) {
  // ÌÉúÏä§ÌÅ¨ ÌÜµÍ≥Ñ Í≥ÑÏÇ∞
  const stats = useMemo(() => {
    const now = new Date();
    const today = format(now, "yyyy-MM-dd");
    const threeDaysLater = format(addDays(now, 3), "yyyy-MM-dd");

    return {
      todo: tasks.filter((t) => t.status === "todo").length,
      inprogress: tasks.filter((t) => t.status === "inprogress").length,
      done: tasks.filter((t) => t.status === "done").length,
      // ÏßÄÏó∞: ÏôÑÎ£åÎêòÏßÄ ÏïäÏïòÍ≥† ÎßàÍ∞êÏùºÏù¥ ÏßÄÎÇ®
      overdue: tasks.filter((t) => {
        if (t.status === "done" || !t.ended_at) return false;
        const endDate = t.ended_at.split("T")[0];
        if (endDate < today) return true;
        // Ïò§ÎäòÏù∏Îç∞ ÏãúÍ∞ÑÍπåÏßÄ Ï≤¥ÌÅ¨
        if (endDate === today && t.use_time && t.end_time) {
          const [hours, minutes] = t.end_time.split(":").map(Number);
          const deadline = new Date();
          deadline.setHours(hours, minutes, 0, 0);
          return now > deadline;
        }
        return false;
      }).length,
      // Í∏¥Í∏â: Ïò§Îäò ÎßàÍ∞ê (ÏïÑÏßÅ ÏßÄÏó∞ ÏïÑÎãò)
      urgent: tasks.filter((t) => {
        if (t.status === "done" || !t.ended_at) return false;
        const endDate = t.ended_at.split("T")[0];
        if (endDate !== today) return false;
        // Ïò§ÎäòÏù¥Í≥† ÏïÑÏßÅ ÏãúÍ∞Ñ Ïïà ÏßÄÎÇ®
        if (t.use_time && t.end_time) {
          const [hours, minutes] = t.end_time.split(":").map(Number);
          const deadline = new Date();
          deadline.setHours(hours, minutes, 0, 0);
          return now <= deadline;
        }
        return true;
      }).length,
      // ÏûÑÎ∞ï: D-3 Ïù¥ÎÇ¥ (Ïò§Îäò Ï†úÏô∏)
      upcoming: tasks.filter((t) => {
        if (t.status === "done" || !t.ended_at) return false;
        const endDate = t.ended_at.split("T")[0];
        return endDate > today && endDate <= threeDaysLater;
      }).length,
    };
  }, [tasks]);

  const total = stats.todo + stats.inprogress + stats.done;
  const completionRate = total > 0 ? Math.round((stats.done / total) * 100) : 0;

  if (compact) {
    // Ïª¥Ìå©Ìä∏ Î™®Îìú (Î™®Î∞îÏùºÏö©)
    return (
      <div className="flex items-center gap-3 px-3 py-2 bg-gray-50 dark:bg-gray-800/50 border-t border-gray-200 dark:border-gray-700">
        <div className="flex items-center gap-2 text-xs">
          {/* ÌÜµÍ≥Ñ */}
          <span className="flex items-center gap-1">
            <span className="w-2 h-2 rounded-full bg-gray-400"></span>
            <span className="text-gray-600 dark:text-gray-400">
              {stats.todo}
            </span>
          </span>
          <span className="flex items-center gap-1">
            <span className="w-2 h-2 rounded-full bg-blue-400"></span>
            <span className="text-gray-600 dark:text-gray-400">
              {stats.inprogress}
            </span>
          </span>
          <span className="flex items-center gap-1">
            <span className="w-2 h-2 rounded-full bg-green-500"></span>
            <span className="text-gray-600 dark:text-gray-400">
              {stats.done}
            </span>
          </span>
          {stats.overdue > 0 && (
            <span className="flex items-center gap-1 text-red-500">
              <span className="w-2 h-2 rounded-full bg-red-500"></span>
              <span>{stats.overdue}</span>
            </span>
          )}
          {stats.urgent > 0 && (
            <span className="flex items-center gap-1 text-orange-500">
              <AlertTriangle size={10} />
              <span>{stats.urgent}</span>
            </span>
          )}
          {/* Íµ¨Î∂ÑÏÑ† */}
          <span className="text-gray-300 dark:text-gray-600">|</span>
          {/* Î≤îÎ°Ä */}
          <span className="w-1 h-3 bg-red-500 rounded-sm" title="ÏßÄÏó∞" />
          <span className="w-1 h-3 bg-green-500 rounded-sm" title="ÏôÑÎ£å" />
          <span className="text-gray-300 dark:text-gray-600">|</span>
          {/* Ïö∞ÏÑ†ÏàúÏúÑ */}
          <span className="text-red-500">‚ñ≤</span>
          <span className="text-yellow-500">‚ñ≤</span>
          <span className="text-green-500">‚ñ≤</span>
        </div>
        <div className="ml-auto text-xs font-medium text-gray-700 dark:text-gray-300">
          {completionRate}%
        </div>
      </div>
    );
  }

  // Í∏∞Î≥∏ Î™®Îìú - CalendarStatsÏôÄ ÎèôÏùºÌïú Ïä§ÌÉÄÏùº
  return (
    <div className="flex items-center justify-between px-4 py-3 bg-gray-50 dark:bg-gray-800/50 border-t border-gray-200 dark:border-gray-700">
      {/* ÏôºÏ™Ω: ÌÜµÍ≥Ñ + Î≤îÎ°Ä */}
      <div className="flex items-center gap-4 sm:gap-6 text-sm flex-wrap">
        {/* ÏÉÅÌÉúÎ≥Ñ ÌÜµÍ≥Ñ */}
        <div className="flex items-center gap-2">
          <span className="w-3 h-3 rounded-full bg-gray-400"></span>
          <span className="text-gray-600 dark:text-gray-400">Ìï†Ïùº</span>
          <span className="font-semibold text-gray-800 dark:text-gray-200">
            {stats.todo}
          </span>
        </div>
        <div className="flex items-center gap-2">
          <span className="w-3 h-3 rounded-full bg-blue-400"></span>
          <span className="text-gray-600 dark:text-gray-400">ÏßÑÌñâÏ§ë</span>
          <span className="font-semibold text-gray-800 dark:text-gray-200">
            {stats.inprogress}
          </span>
        </div>
        <div className="flex items-center gap-2">
          <span className="w-3 h-3 rounded-full bg-green-500"></span>
          <span className="text-gray-600 dark:text-gray-400">ÏôÑÎ£å</span>
          <span className="font-semibold text-gray-800 dark:text-gray-200">
            {stats.done}
          </span>
        </div>

        {/* Í∏¥Í∏â/ÏûÑÎ∞ï/ÏßÄÏó∞ ÌëúÏãú */}
        {(stats.urgent > 0 || stats.upcoming > 0 || stats.overdue > 0) && (
          <>
            <span className="text-gray-300 dark:text-gray-600">|</span>
            {stats.urgent > 0 && (
              <div className="flex items-center gap-1 text-orange-500">
                <AlertTriangle size={14} />
                <span className="text-xs font-medium">Ïò§Îäò {stats.urgent}</span>
              </div>
            )}
            {stats.upcoming > 0 && (
              <div className="flex items-center gap-1 text-yellow-600 dark:text-yellow-500">
                <Clock size={14} />
                <span className="text-xs font-medium">
                  D-3 Ïù¥ÎÇ¥ {stats.upcoming}
                </span>
              </div>
            )}
            {stats.overdue > 0 && (
              <div className="flex items-center gap-2 text-red-500">
                <span className="w-3 h-3 rounded-full bg-red-500"></span>
                <span>ÏßÄÏó∞</span>
                <span className="font-semibold">{stats.overdue}</span>
              </div>
            )}
          </>
        )}

        {/* Íµ¨Î∂ÑÏÑ† */}
        <span className="text-gray-300 dark:text-gray-600">|</span>

        {/* Î≤îÎ°Ä */}
        <div className="flex items-center gap-3">
          <span className="flex items-center gap-1" title="ÏßÄÏó∞Îêú ÌÉúÏä§ÌÅ¨">
            <span className="w-1 h-4 bg-red-500 rounded-sm" />
            <span className="text-gray-600 dark:text-gray-400">ÏßÄÏó∞</span>
          </span>
          <span className="flex items-center gap-1" title="ÏôÑÎ£åÎêú ÌÉúÏä§ÌÅ¨">
            <span className="w-1 h-4 bg-green-500 rounded-sm" />
            <span className="text-gray-600 dark:text-gray-400">ÏôÑÎ£å</span>
          </span>
        </div>

        {/* Íµ¨Î∂ÑÏÑ† */}
        <span className="text-gray-300 dark:text-gray-600">|</span>

        {/* Ïö∞ÏÑ†ÏàúÏúÑ Î≤îÎ°Ä */}
        <div className="flex items-center gap-3">
          <span className="flex items-center gap-1">
            <span className="text-red-500">‚ñ≤</span>
            <span className="text-gray-600 dark:text-gray-400">ÎÜíÏùå</span>
          </span>
          <span className="flex items-center gap-1">
            <span className="text-yellow-500">‚ñ≤</span>
            <span className="text-gray-600 dark:text-gray-400">Î≥¥ÌÜµ</span>
          </span>
          <span className="flex items-center gap-1">
            <span className="text-green-500">‚ñ≤</span>
            <span className="text-gray-600 dark:text-gray-400">ÎÇÆÏùå</span>
          </span>
        </div>
      </div>

      {/* Ïò§Î•∏Ï™Ω: ÏßÑÌñâÎ•† */}
      <div className="flex items-center gap-3">
        <div className="hidden sm:block w-24 h-2 bg-gray-200 dark:bg-gray-700 rounded-full overflow-hidden">
          <div
            className="h-full bg-main-500 transition-all duration-300"
            style={{ width: `${completionRate}%` }}
          />
        </div>
        <span className="text-sm font-medium text-gray-700 dark:text-gray-300">
          {completionRate}% ÏôÑÎ£å
        </span>
      </div>
    </div>
  );
}
