// components/features/task/shared/DateFields.tsx
import { Clock } from "lucide-react";
import DatePicker from "@/components/ui/DatePicker";
import { useState, useRef, useEffect } from "react";

export function DateFields({
  startDate,
  endDate,
  startTime,
  endTime,
  projectStartedAt,
  projectEndedAt,
  useTime = false,
  error,
  disabled,
  onStartDateChange,
  onEndDateChange,
  onStartTimeChange,
  onEndTimeChange,
  onUseTimeChange,
}: {
  startDate: string;
  endDate: string;
  startTime: string;
  endTime: string;
  useTime: boolean;
  projectStartedAt?: string;
  projectEndedAt?: string;
  error?: string;
  disabled?: boolean;
  onStartDateChange: (v: string) => void;
  onEndDateChange: (v: string) => void;
  onStartTimeChange: (v: string) => void;
  onEndTimeChange: (v: string) => void;
  onUseTimeChange: (v: boolean) => void;
}) {
  // ÌîÑÎ°úÏ†ùÌä∏ ÏÉÅÌÉúÏóê Îî∞Î•∏ ÎπÑÌôúÏÑ±Ìôî Ïó¨Î∂Ä Í≤∞Ï†ï
  const isProjectEnded = (() => {
    if (!projectEndedAt) return false;
    const today = new Date().toISOString().split("T")[0]; // YYYY-MM-DD ÌòïÏãù
    return today > projectEndedAt;
  })();

  // 3Ô∏è‚É£ Ï¢ÖÎ£å ÌõÑ ÏÉÅÌÉúÏóêÏÑúÎäî ÏùºÏ†ï Ï∂îÍ∞Ä Î∂àÍ∞Ä (ÏùΩÍ∏∞ Ï†ÑÏö©)
  const isReadOnly = isProjectEnded;
  const finalDisabled = disabled || isReadOnly;

  return (
    <div className="space-y-4">
      {/* ÎÇ†Ïßú ÏÑ†ÌÉù */}
      <div className="grid grid-cols-2 gap-4">
        <DatePicker
          label="ÏãúÏûëÏùº"
          icon="calendarPlus"
          value={startDate}
          onChange={onStartDateChange}
          minDate={projectStartedAt}
          maxDate={endDate || projectEndedAt}
          disabled={finalDisabled}
        />

        <DatePicker
          label="ÎßàÍ∞êÏùº"
          icon="calendarCheck"
          value={endDate}
          onChange={onEndDateChange}
          minDate={startDate || projectStartedAt}
          maxDate={projectEndedAt}
          error={error}
          disabled={finalDisabled}
        />
      </div>

      {/* ÌîÑÎ°úÏ†ùÌä∏ Ï¢ÖÎ£å ÌõÑ ÏùΩÍ∏∞Ï†ÑÏö© ÏïàÎÇ¥ */}
      {isReadOnly && (
        <div className="p-3 bg-amber-50 dark:bg-amber-900/20 border border-amber-200 dark:border-amber-800 rounded-lg">
          <p className="text-amber-600 dark:text-amber-400 text-sm font-medium">
            ‚ö†Ô∏è ÌîÑÎ°úÏ†ùÌä∏Í∞Ä Ï¢ÖÎ£åÎêòÏñ¥ ÏùºÏ†ïÏùÑ Ï∂îÍ∞ÄÌï† Ïàò ÏóÜÏäµÎãàÎã§ (ÏùΩÍ∏∞ Ï†ÑÏö©)
          </p>
        </div>
      )}

      {/* ÏãúÍ∞Ñ ÏßÄÏ†ï ÌÜ†Í∏Ä */}
      <label
        className={`flex items-center gap-2 text-sm select-none ${
          finalDisabled ? "cursor-not-allowed opacity-60" : "cursor-pointer"
        }`}
      >
        <input
          type="checkbox"
          checked={useTime}
          onChange={(e) => onUseTimeChange(e.target.checked)}
          disabled={finalDisabled}
          className="w-4 h-4 rounded border-gray-300 dark:border-gray-600 
            text-main-600 focus:ring-main-500 dark:bg-gray-700
            disabled:cursor-not-allowed"
        />
        <span className="text-gray-600 dark:text-gray-400 font-medium">
          ÏãúÍ∞Ñ ÏßÄÏ†ï
        </span>
      </label>

      {/* ÏãúÍ∞Ñ ÏûÖÎ†• */}
      {useTime && (
        <div className="grid grid-cols-2 gap-4">
          <TimePicker
            label="ÏãúÏûë ÏãúÍ∞Ñ"
            value={startTime}
            onChange={onStartTimeChange}
            disabled={finalDisabled}
          />
          <TimePicker
            label="Ï¢ÖÎ£å ÏãúÍ∞Ñ"
            value={endTime}
            onChange={onEndTimeChange}
            disabled={finalDisabled}
          />
        </div>
      )}
    </div>
  );
}

function TimePicker({
  label,
  value,
  onChange,
  disabled,
}: {
  label: string;
  value: string;
  onChange: (v: string) => void;
  disabled?: boolean;
}) {
  const [isFocused, setIsFocused] = useState(false);
  const [inputValue, setInputValue] = useState(value);
  const containerRef = useRef<HTMLDivElement>(null);

  const hours = Array.from({ length: 15 }, (_, i) =>
    (i + 9).toString().padStart(2, "0")
  ); // 09:00 ~ 23:00
  const minutes = ["00", "15", "30", "45"]; // 15Î∂Ñ Í∞ÑÍ≤©

  const [hour, minute] = value ? value.split(":") : ["09", "00"];

  // valueÍ∞Ä Î≥ÄÍ≤ΩÎêòÎ©¥ inputValueÎèÑ ÎèôÍ∏∞Ìôî
  useEffect(() => {
    setInputValue(value);
  }, [value]);

  // Ïô∏Î∂Ä ÌÅ¥Î¶≠ Í∞êÏßÄ
  useEffect(() => {
    const handleClickOutside = (e: MouseEvent) => {
      if (
        containerRef.current &&
        !containerRef.current.contains(e.target as Node)
      ) {
        setIsFocused(false);
      }
    };

    if (isFocused) {
      document.addEventListener("mousedown", handleClickOutside);
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [isFocused]);

  const handleTimeChange = (newHour: string, newMinute: string) => {
    const newTime = `${newHour}:${newMinute}`;
    setInputValue(newTime);
    onChange(newTime);
  };

  // ÏßÅÏ†ë ÏûÖÎ†• Ï≤òÎ¶¨ (ÏûêÎèô Ìè¨Îß∑ÌåÖ Ìè¨Ìï®)
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    let val = e.target.value.replace(/[^0-9:]/g, ""); // Ïà´ÏûêÏôÄ ÏΩúÎ°†Îßå ÌóàÏö©

    // ÏûêÎèôÏúºÎ°ú ÏΩúÎ°† Ï∂îÍ∞Ä (4ÏûêÎ¶¨ ÏûÖÎ†• Ïãú)
    if (val.length === 4 && !val.includes(":")) {
      val = val.slice(0, 2) + ":" + val.slice(2);
    }

    setInputValue(val);

    // HH:MM Ìè¨Îß∑ Í≤ÄÏ¶ù
    const timeRegex = /^([0-1][0-9]|2[0-3]):([0-5][0-9])$/;
    if (timeRegex.test(val)) {
      onChange(val);
    }
  };

  // ÏûÖÎ†• ÌïÑÎìúÏóêÏÑú Ìè¨Ïª§Ïä§ ÏïÑÏõÉÎê† Îïå Ìè¨Îß∑ Î≥¥Ï†ï
  const handleBlur = () => {
    if (inputValue && !inputValue.match(/^([0-1][0-9]|2[0-3]):([0-5][0-9])$/)) {
      // ÏûòÎ™ªÎêú Ìè¨Îß∑Ïù¥Î©¥ Ïù¥Ï†Ñ Í∞íÏúºÎ°ú Î≥µÏõê
      setInputValue(value);
    }
  };

  return (
    <div ref={containerRef} className="relative">
      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1.5">
        {label}
      </label>

      {/* ÏßÅÏ†ë ÏûÖÎ†• ÌïÑÎìú */}
      <div className="relative">
        <input
          type="text"
          value={inputValue}
          onChange={handleInputChange}
          onKeyDown={(e) => {
            if (e.key === "ArrowDown" || e.key === "Enter") {
              e.preventDefault();
              setIsFocused(true);
            } else if (e.key === "Escape") {
              setIsFocused(false);
            }
          }}
          onFocus={() => setIsFocused(true)}
          onBlur={handleBlur}
          placeholder="09:00"
          disabled={disabled}
          className={`
            w-full h-10 pl-3 pr-10
            border border-gray-300 dark:border-gray-600 rounded-lg 
            input-focus-style
            bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100
            placeholder:text-gray-400 dark:placeholder:text-gray-500
            disabled:bg-gray-100 dark:disabled:bg-gray-800 disabled:cursor-not-allowed disabled:opacity-60
            text-sm transition-all
          `}
        />
        <Clock
          className="absolute right-3 top-1/2 -translate-y-1/2 w-4 h-4 
          text-gray-400 dark:text-gray-500 pointer-events-none"
        />
      </div>

      {/* Îπ†Î•∏ ÏÑ†ÌÉù Ìå®ÎÑê (Ìè¨Ïª§Ïä§ Ïãú ÌëúÏãú) */}
      {isFocused && !disabled && (
        <div
          className="absolute z-50 mt-1 w-full
          bg-white dark:bg-gray-800 
          border border-gray-200 dark:border-gray-700 
          rounded-lg shadow-lg p-3"
          onMouseDown={(e) => e.preventDefault()} // Ìè¨Ïª§Ïä§ Ïú†ÏßÄ ÏúÑÌï¥ Í∏∞Î≥∏ ÎèôÏûë Î∞©ÏßÄ
        >
          <div className="text-xs text-gray-500 dark:text-gray-400 mb-2 font-medium">
            ÏãúÍ∞Ñ ÏÑ†ÌÉù
          </div>
          <div className="flex gap-2">
            {/* ÏãúÍ∞Ñ ÏÑ†ÌÉù */}
            <div className="flex-1">
              <div className="text-xs text-gray-500 dark:text-gray-400 mb-1.5">
                Ïãú
              </div>
              <div className="max-h-32 overflow-y-auto border border-gray-200 dark:border-gray-700 rounded">
                {hours.map((h) => (
                  <div
                    key={h}
                    onClick={() => handleTimeChange(h, minute)}
                    className={`px-2 py-1.5 text-sm cursor-pointer text-center
                      ${
                        hour === h
                          ? "bg-main-50 dark:bg-main-900/30 text-main-600 dark:text-main-400 font-medium"
                          : "text-gray-700 dark:text-gray-300 hover:bg-gray-50 dark:hover:bg-gray-700"
                      }`}
                  >
                    {h}
                  </div>
                ))}
              </div>
            </div>

            {/* Î∂Ñ ÏÑ†ÌÉù */}
            <div className="flex-1">
              <div className="text-xs text-gray-500 dark:text-gray-400 mb-1.5">
                Î∂Ñ
              </div>
              <div className="border border-gray-200 dark:border-gray-700 rounded">
                {minutes.map((m) => (
                  <div
                    key={m}
                    onClick={() => handleTimeChange(hour, m)}
                    className={`px-2 py-1.5 text-sm cursor-pointer text-center
                      ${
                        minute === m
                          ? "bg-main-50 dark:bg-main-900/30 text-main-600 dark:text-main-400 font-medium"
                          : "text-gray-700 dark:text-gray-300 hover:bg-gray-50 dark:hover:bg-gray-700"
                      }`}
                  >
                    {m}
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
